import { getPoolVersionId } from '../shared/config/pool';
import { api } from './api_shared'

export const getTradeHistory = async (
  chainId,
  poolAddress,
  accountAddress,
  symbolId
) => {
  const versionId = getPoolVersionId(poolAddress)
  if (versionId.failed) {
    return { success: false, response: versionId.response }
  }
  return api(versionId).getTradeHistory(chainId, poolAddress, accountAddress, symbolId)
  // if (LITE_VERSIONS.includes(version)) {
  //   return getTradeHistoryV2l(chainId, poolAddress, accountAddress, symbolId);
  // } else if (version === 'option') {
  //   return apiOption.getTradeHistory(chainId, poolAddress, accountAddress, symbolId);
  // } else if (version === 'v2_lite_dpmm') {
  //   return apiV2lDpmm.getTradeHistory(chainId, poolAddress, accountAddress, symbolId)
  // } else if (version === 'v2_dpmm') {
  //   return apiV2Dpmm.getTradeHistory(chainId, poolAddress, accountAddress, symbolId)
  // } else if (version === 'v1') {
  //   return apiV1.getTradeHistory(chainId, poolAddress, accountAddress);
  // }
};
