import { AppError } from "../error/error";

// export let Env;
// (function (Env) {
//   Env["PROD"] = "prod";
//   Env["DEV"] = "dev";
//   Env["TESTNET"] = "testnet";
// })(Env || (Env = {}));

export const Env = {
  PROD: 'prod',
  TESTNET: 'testnet',
  DEV: 'dev',
};
Object.freeze(Env);

export const DeriEnv = (function(Env) {
  let _env = Env.DEV
  return {
    get() {
      return _env
    },
    set(env) {
      if (Object.values(Env).includes(env)) {
        _env = env
      } else {
        throw new AppError('INVALID_DERI_ENV', env)
      }
    }
  }
})(Env)
