import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import { Provider } from 'mobx-react';
import reportWebVitals from './reportWebVitals';
import { HashRouter } from 'react-router-dom';
import { DeriEnv } from './lib/web3js/index';
import Wallet from './model/Wallet';
import Trading from './model/Trading';
import PoolManager from './model/PoolManager';
import version from './model/Version';
import intl from './model/Intl';
import loading from './model/Loading';
import type from './model/Type';
import {positions, Provider as AlertProvider } from 'react-alert'
import MessageTemplate from './components/Message/MessageTemplate';
const wallet = new Wallet();
const trading = new Trading();
const poolManager = new PoolManager();
if(process.env.NODE_ENV === 'production') {
  DeriEnv.set('prod')
}
// DeriEnv.set('testnet')
DeriEnv.set('prod')
ReactDOM.render(
  // <React.StrictMode>
    <HashRouter>
      <Provider wallet={wallet} poolManager={poolManager} type={type}  trading={trading} version={version} intl={intl} loading={loading}>
        <AlertProvider position ={positions.TOP_RIGHT}  timeout ={5000}  offset ='30px'  template={MessageTemplate}>
          <App />
        </AlertProvider>
      </Provider>
    </HashRouter>
  // </React.StrictMode>
  ,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bi  t.ly/CRA-vitals
reportWebVitals();
