export const ErrorCode = {
  WEB3_WALLET_NOT_FOUND: 'WEB3_WALLET_NOT_FOUND',
  BROWSER_NOT_SUPPORTED: 'BROWSER_NOT_SUPPORTED',
  INVALID_DERI_ENV: 'INVALID_DERI_ENV',
  INVALID_VERSION: 'INVALID_VERSION',
  INVALID_VERSION_ID: 'INVALID_VERSION_ID',
  INVALID_CHAIN_ID: 'INVALID_CHAIN_ID',
  INVALID_ADDRESS: 'INVALID_ADDRESS',
  INVALID_TOKEN_ID: 'INVALID_TOKEN_ID',
  INVALID_TOKEN_ID_IN_RANGE: 'INVALID_TOKEN_ID_IN_RANGE',
  INVALID_SYMBOl: 'INVALID_SYMBOl',
  INVALID_BASE_TOKEN: 'INVALID_BASE_TOKEN',
  INVALID_AMOUNT: 'INVALID_AMOUNT',
  INVALID_TRADE_VOLUME: 'INVALID_TRADE_VOLUME',
  INVALID_DATA: 'INVALID_DATA',
  INVALID_CACHE: 'INVALID_CACHE',
  CONFIG_LIST_NOT_FOUND: 'CONFIG_LIST_NOT_FOUND',
  CONFIG_NOT_FOUND: 'CONFIG_NOT_FOUND',
  RPC_NODE_ALL_UNAVAILABLE: 'RPC_NODE_ALL_UNAVAILABLE',
  INVALID_JSON_RPC_RESPONSE: 'INVALID_JSON_RPC_RESPONSE',
  JSON_RPC_CALL_TIMEOUT: 'JSON_RPC_CALL_TIMEOUT',
  REST_API_CALL_TIMEOUT: 'REST_API_CALL_TIMEOUT',
  INSUFFICIEN_MARGIN: 'INSUFFICIEN_MARGIN',
  INSUFFICIENT_BALANCE: 'INSUFFICIENT_BALANCE',
  INSUFFICIEN_LIQUIDITY: 'INSUFFICIEN_LIQUIDITY',
  PRICE_EXPIRED: 'PRICE_EXPIRED',
  VOLATILITY_EXPIRED: 'VOLATILITY_EXPIRED',
  DEADLINE_EXPIRED: 'DEADLINE_EXPIRED',
  OUT_OF_GAS: 'OUT_OF_GAS',
  TRANSACTION_REVERTED: 'TRANSACTION_REVERTED',
  API_REQUEST_TIMEOUT: 'API_REQUEST_TIMEOUT',
  UNKOWN_ERROR: 'UNKOWN_ERROR',
};
Object.freeze(ErrorCode);

export let ErrorMessage;
(function (ErrorMessage) {
  // client
  ErrorMessage.WEB3_WALLET_NOT_FOUND = () => 'web3 wallet is not found';
  ErrorMessage.BROWSER_NOT_SUPPORTED = () => 'browser is not supported';

  // input error
  ErrorMessage.INVALID_DERI_ENV = (env) => `invalid deri env '${env}'`;
  ErrorMessage.INVALID_VERSION = (version) =>
    `invalid pool version '${version}'`;
  ErrorMessage.INVALID_VERSION_ID = (versionId) =>
    `invalid pool version id '${versionId}'`;
  ErrorMessage.INVALID_CHAIN_ID = (chainId) => `invalid chain id '${chainId}'`;
  ErrorMessage.INVALID_ADDRESS = (address) =>
    `invalid eth address '${address}'`;
  ErrorMessage.INVALID_TOKEN_ID = (tokenId) => `invalid token id'${tokenId}'`;
  ErrorMessage.INVALID_TOKEN_ID_IN_RANGE = ({ tokenId, tokenIds, name }) =>
    `invalid ${name} '${tokenId}' for ${name}s(${tokenIds.join(',')})`;
  ErrorMessage.INVALID_AMOUNT = (amount) => `invalid amount '${amount}'`;
  ErrorMessage.INVALID_TRADE_VOLUME = (volume) =>
    `invalid trade volume '${volume}''`;
  ErrorMessage.INVALID_SYMBOL = (symbol) => `invalid symbol'${symbol}'`;
  ErrorMessage.INVALID_BASE_TOKEN = (bToken) => `invalid base token'${bToken}'`;
  ErrorMessage.INVALID_DATA = (data) =>
    `invalid data '${JSON.stringify(data)}'`;
  ErrorMessage.INVALID_CACHE = ({name, data}) =>
    `invalid cache value '${JSON.stringify(data)}' for key '${name}'`;
  ErrorMessage.CONFIG_LIST_NOT_FOUND = ({ name, env }) =>
    `the config list is not found with env ${env} by ${name}()`;
  ErrorMessage.CONFIG_NOT_FOUND = ({ name, args = [] }) =>
    `the config is not found by ${name}(${args.join(',')})`;

  // network error
  ErrorMessage.RPC_NODE_ALL_UNAVAILABLE = (chainId) =>
    `all rpc nodes are not available for chain id '${chainId}'`;
  ErrorMessage.INVALID_JSON_RPC_RESPONSE = () => 'invalid JSON RPC response';
  ErrorMessage.REST_API_CALL_TIMEOUT = (url) =>
    `REST API request is timeout: ${url}`;
  ErrorMessage.JSON_RPC_CALL_TIMEOUT = ({ name, args = [] }) =>
    `JSON RPC call timeout: ${name}(${args.join(',')})`;

  // tansaction error
  ErrorMessage.INSUFFICIEN_MARGIN = () => `insufficient margin`;
  ErrorMessage.INSUFFICIENT_BALANCE = () => 'insufficient balance';
  ErrorMessage.INSUFFICIEN_LIQUIDITY = () => 'insufficient pool liquidity';
  ErrorMessage.PRICE_EXPIRED = () => 'price is expired';
  ErrorMessage.VOLATILITY_EXPIRED = () => 'volatility is expired';
  ErrorMessage.DEADLINE_EXPIRED = () => 'deadline is expired';
  ErrorMessage.OUT_OF_GAS = () => 'transaction is out of gas';
  ErrorMessage.TRANSACTION_REVERTED = () => 'transaction is reverted';

  // api server error
  ErrorMessage.API_REQUEST_TIMEOUT = (url) => `api request '${url}' is timeout`;

  // common error
  ErrorMessage.UNKOWN_ERROR = ({ err, name, args = [] }) =>
    `unkown error: ${name}(${args.join(',')}) with ${err}`;
})(ErrorMessage || (ErrorMessage = {}));
Object.freeze(ErrorMessage);

export let ErrorName;
(function (ErrorName) {
  const errorCodes = Object.keys(ErrorMessage);
  for (let v of errorCodes) {
    if (['WEB3_WALLET_NOT_FOUND', 'BROWSER_NOT_SUPPORTED'].includes(v)) {
      ErrorName[v] = 'ClientError';
    } else if (
      [
        'INVALID_DERI_ENV',
        'INVALID_VERSION',
        'INVALID_VERSION_ID',
        'INVALID_CHAIN_ID',
        'INVALID_ADDRESS',
        'INVALID_TOKEN_ID',
        'INVALID_TOKEN_ID_IN_RANGE',
        'INVALID_SYMBOl',
        'INVALID_BASE_TOKEN',
        'INVALID_AMOUNT',
        'INVALID_TRADE_VOLUME',
        'INVALID_DATA',
        'INVALID_CACHE',
        'CONFIG_LIST_NOT_FOUND',
        'CONFIG_NOT_FOUND',
      ].includes(v)
    ) {
      ErrorName[v] = 'InputError';
    } else if (
      [
        'RPC_NODE_ALL_UNAVAILABLE',
        'INVALID_JSON_RPC_RESPONSE',
        'JSON_RPC_CALL_TIMEOUT',
        'REST_API_CALL_TIMEOUT',
      ].includes(v)
    ) {
      ErrorName[v] = 'NetworkError';
    } else if (
      [
        'INSUFFICIEN_MARGIN',
        'INSUFFICIENT_BALANCE',
        'INSUFFICIEN_LIQUIDITY',
        'PRICE_EXPIRED',
        'VOLATILITY_EXPIRED',
        'DEADLINE_EXPIRED',
        'OUT_OF_GAS',
        'TRANSACTION_REVERTED',
      ].includes(v)
    ) {
      ErrorName[v] = 'TransactionError';
    } else if (['API_REQUEST_TIMEOUT'].includes(v)) {
      ErrorName[v] = 'ApiServerError';
    } else {
      ErrorName[v] = 'UnkownError';
    }
  }
})(ErrorName || (ErrorName = {}));
