import { clp2PoolFactory } from '../factory/contract';
import { getLpConfig } from '../config/pool_v1';
import { catchApiError, catchTxApiError} from '../utils';
import { checkChainId, checkAddress } from '../config/check';
import { ERC20Factory } from '../contract';

export const getClp2LiquidityInfo = async (
  chainId,
  poolAddress,
  accountAddress
) => {
  return catchApiError(async () => {
    chainId = checkChainId(chainId)
    poolAddress = checkAddress(poolAddress)
    accountAddress = checkAddress(accountAddress)
    const { bTokenAddress } = await getLpConfig(chainId, poolAddress);
    const clp2Pool = clp2PoolFactory(chainId, poolAddress);
    const bToken = ERC20Factory(chainId, bTokenAddress);
    const [liquidity, bTokenBalance, shares] = await Promise.all([
      bToken.balanceOf(poolAddress),
      bToken.balanceOf(accountAddress),
      clp2Pool.getLiquidity(accountAddress),
    ]);
    return {
      poolLiquidity: liquidity.toString(),
      bTokenBalance: bTokenBalance.toString(),
      shares: shares.toString(),
      shareValue: '1',
    };
  });
};


export const isClp2Unlocked = async (chainId, poolAddress, accountAddress) => {
  return catchApiError(async () => {
    chainId = checkChainId(chainId)
    poolAddress = checkAddress(poolAddress)
    accountAddress = checkAddress(accountAddress)
    const { bTokenAddress } = await getLpConfig(chainId, poolAddress);
    const bToken = ERC20Factory(chainId, bTokenAddress);
    return await bToken.isUnlocked(accountAddress, poolAddress);
  });
};
export const getClp2WalletBalance = async (
  chainId,
  poolAddress,
  accountAddress
) => {
  return catchApiError(async () => {
    chainId = checkChainId(chainId)
    poolAddress = checkAddress(poolAddress)
    accountAddress = checkAddress(accountAddress)
    const { bTokenAddress } = await getLpConfig(chainId, poolAddress);
    const bToken = ERC20Factory(chainId, bTokenAddress);
    const balance = await bToken.balanceOf(accountAddress);
    return balance.toString();
  });
};

export const addClp2Liquidity = async (
  chainId,
  poolAddress,
  accountAddress,
  amount
) => {
  return catchTxApiError(async () => {
    chainId = checkChainId(chainId)
    poolAddress = checkAddress(poolAddress)
    accountAddress = checkAddress(accountAddress)
    await getLpConfig(chainId, poolAddress);
    const clp2Pool = clp2PoolFactory(chainId, poolAddress);
    return await clp2Pool.addLiquidity(accountAddress, amount);
  });
};

export const removeClp2Liquidity = async (
  chainId,
  poolAddress,
  accountAddress,
  amount
) => {
  return catchTxApiError(async () => {
    chainId = checkChainId(chainId)
    poolAddress = checkAddress(poolAddress)
    accountAddress = checkAddress(accountAddress)
    await getLpConfig(chainId, poolAddress);
    const clp2Pool = clp2PoolFactory(chainId, poolAddress);
    return await clp2Pool.removeLiquidity(accountAddress, amount);
  });
};

export const unlockClp2 = async (chainId, poolAddress, accountAddress) => {
  return catchTxApiError(async () => {
    chainId = checkChainId(chainId)
    poolAddress = checkAddress(poolAddress)
    accountAddress = checkAddress(accountAddress)
    const { bTokenAddress } = await getLpConfig(chainId, poolAddress);
    const bToken = ERC20Factory(chainId, bTokenAddress);
    return await bToken.unlock(accountAddress, poolAddress);
  });
};

