import styled from 'styled-components'
import langIcon from '../../assets/img/lang-icon.svg'
import Tooltip from '../Tooltip/Tooltip'
import languages from '../../locales/lang.json'
import classNames from 'classnames'
import { inject, observer } from 'mobx-react'
const Wrapper = styled.div`
  display : flex;
  align-items: center;
  img {
    cursor : pointer;
  }
`

const Panel = styled.div`
  color : #93A1B8;
  .item:hover,.item.selected {
    color : #E0ECFF;
    cursor : pointer;
  }
  .__react_component_tooltip.show{
    width : 180px;
  }
  .item {
    padding : 16px;
  }
`

function LangSelector({intl}){
  const calculatePosition = (position,currentEvent,currentTarget,refNode,place,desiredPlace,effect,offset) => {
    const rect = currentTarget.getBoundingClientRect();
    const top = rect.top + rect.height + 12;
    const left = (rect.left - refNode.offsetWidth + rect.width)
    return {top:top + offset.top,left : left}
  }
  
  return (
    <Wrapper className='lang-select'>
      <img src={langIcon} alt='lang Selector' data-tip data-for='lang-selector' data-event='click'  />
      <Tooltip width ='150' offset={{top : 16}} id='lang-selector' place='bottom' overridePosition={calculatePosition}
         resizeHide={false} className='lang-select-tooltip' type="info" globalEventOff='click' backgroundColor='#203B60' clickable>
        <Panel>
          {Object.keys(languages).map((lang,index) => <div key={index} className={classNames('item',{selected : lang === intl.locale})}>{languages[lang]}</div>)}
        </Panel>
      </Tooltip>
    </Wrapper>
  )
}
export default inject('intl')(observer(LangSelector))