import { observable, action, computed, makeObservable } from "mobx";
import Pool from './Pool'
import apiProxy from './ApiProxy'
import { DeriEnv, openConfigListCache } from '../lib/web3js/index';
import { FUTURE, FUTURE_INNO, OPTION, FUTURE_OPEN } from "../lib/web3js/shared";
import blackList from "../blackList.json"
const openBlackList = blackList['openPoolList']
export default class PoolManager {
  pools = []
  lpPools = []
  openPools = []
  constructor() {
    makeObservable(this, {
      pools: observable,
      setPools: action,
      setLpPools: action,
      v2Pools: computed,
      innoPools: computed,
      setOpenPools: action,
      optionsPools: computed,
      ammPools: computed,
      futurePools: computed
    })
  }

  async load() {
    const pools = await apiProxy.request('getPoolConfigList');
    this.loadDoubleMingingPool()
    const mapPools = pools.map(pool => new Pool(pool))
    this.setPools(mapPools)
    await openConfigListCache.update()
    return mapPools;
  }

  async loadByType(type) {
    let pools = await apiProxy.request('getPoolConfigList');
    if(type === 'future'){
      await openConfigListCache.update()
      let open = await apiProxy.request('getPoolOpenConfigList');
      pools = [...pools,...open]
    }
    return pools.filter(p => p.type === type).map(pool => new Pool(pool))
  }

  async loadOpen() {
    await openConfigListCache.update()
    const pools = await apiProxy.request('getPoolOpenConfigList');
    const mapPools = pools.map(pool => new Pool(pool))
    let openPools = mapPools.filter(p => !openBlackList.includes(p.address))
    this.setOpenPools(openPools)
    return mapPools
  }

  async loadDoubleMingingPool() {
    const pools = await apiProxy.request('getLpConfigList');
    if (pools.length) {
      const mapPools = pools.map(pool => new Pool(pool))
      this.setLpPools(mapPools)
      return mapPools;
    }
  }

  setPools(pools) {
    this.pools = pools;
  }
  setLpPools(pools) {
    this.lpPools = pools
  }

  setOpenPools(pools) {
    this.openPools = pools
  }

  get ammPools() {
    return this.pools.filter(p => p.type === 'option' || p.type === 'future')
  }

  get v2Pools() {
    return this.pools.filter(p => p.version === FUTURE)
  }

  get innoPools() {
    return this.pools.filter(p => p.version === FUTURE_INNO)
  }

  get optionsPools() {
    return this.pools.filter(p => p.version === OPTION)
  }

  get futurePools() {
    return this.pools.filter(p => p.type === 'future')
  }


}