import { getPoolVersionId } from '../shared/config/pool';
import { api } from './api_shared'

export const addLiquidity = async (
  chainId,
  poolAddress,
  accountAddress,
  amount,
  bTokenId,
) => {
  const versionId = getPoolVersionId(poolAddress)
  if (versionId.failed) {
    return { success: false, response: versionId.response }
  }
  return api(versionId).addLiquidity(
    chainId,
    poolAddress,
    accountAddress,
    amount,
    bTokenId
  );
  // if (LITE_VERSIONS.includes(versionId)) {
  //   return addLiquidityV2l(chainId, poolAddress, accountAddress, amount)
  // } else if (versionId === 'option') {
  //   return apiOption.addLiquidity(chainId, poolAddress, accountAddress, amount);
  // } else if (versionId === 'v2_lite_dpmm') {
  //   return apiV2lDpmm.addLiquidity(chainId, poolAddress, accountAddress, amount);
  // } else if (versionId === 'v2_dpmm') {
  //   return apiV2Dpmm.addLiquidity(chainId, poolAddress, accountAddress, amount, bTokenId);
  // } else if (versionId === 'v1') {
  //   return apiV1.addLiquidity(chainId, poolAddress, accountAddress, amount);
  // }
}

