import { ObjectCache } from '../../shared/cache';
import { checkAmount, checkApiInput, getPoolConfig } from '../../shared/config';
import { AppError } from '../../shared/error/error';
import { ErrorCode } from '../../shared/error/error_code';
import { catchApiError, bg } from '../../shared/utils';
import {
  calculateMaxRemovableShares,
  calculateShareValue,
} from '../../v1/calculation';
import { everlastingOptionFactory } from '../factory/pool';
import { volatilitiesCache } from '../utils';

const _getLiquidityInfo = async (chainId, poolAddress, accountAddress) => {
  [chainId, poolAddress, accountAddress ] = checkApiInput(
    chainId,
    poolAddress,
    accountAddress
  );
  const optionPool = everlastingOptionFactory(chainId, poolAddress);
  await optionPool.init()
  const lToken = optionPool.lToken
  const [
    lTokenBalance,
    lTokenTotalSupply,
  ] = await Promise.all([
    lToken.balanceOf(accountAddress),
    lToken.totalSupply(),
  ]);

  let symbols
  if (!optionPool.symbols) {
    symbols = await optionPool.getSymbols()
  } else {
    symbols = optionPool.symbols
  }
  const symbolVolatilities = await volatilitiesCache.get(symbols.map((s) => s.symbol))
  const state = await optionPool.viewer.getPoolStates(poolAddress, [], symbolVolatilities.map((v) => v.volatility))
  const { poolState } = state;
  const { initialMarginRatio, liquidity, totalDynamicEquity } = poolState;
  const cost = symbols.reduce((acc, s) => acc.plus(s.tradersNetCost), bg(0));
  const value = symbols.reduce(
    (acc, s) =>
      acc.plus(bg(s.tradersNetVolume).times(s.strikePrice).times(s.multiplier)),
    bg(0)
  );
  const shareValue = calculateShareValue(
      lTokenTotalSupply,
      totalDynamicEquity
    ).toString()
  ObjectCache.set(poolAddress, 'liquidity_info', {
    lTokenBalance: lTokenBalance.toString(),
    liquidity: liquidity.toString(),
    lTokenTotalSupply: lTokenTotalSupply.toString(),
    totalDynamicEquity: totalDynamicEquity.toString(),
    shareValue,
  });
  return {
    totalSupply: lTokenTotalSupply.toString(),
    poolLiquidity: liquidity.toString(),
    shares: lTokenBalance.toString(),
    shareValue,
    maxRemovableShares: calculateMaxRemovableShares(
      lTokenBalance,
      lTokenTotalSupply,
      liquidity,
      value,
      cost,
      bg(initialMarginRatio).times(10)
    ).toString(),
  };
};

export const getUserStakeInfo = async (
  chainId,
  poolAddress,
  accountAddress
) => {
  return catchApiError(async () => {
    [chainId, poolAddress, accountAddress] = checkApiInput(
      chainId,
      poolAddress,
      accountAddress
    );
    const optionPool = everlastingOptionFactory(chainId, poolAddress);
    await optionPool.init();
    const liquidity = await optionPool.lToken.balanceOf(accountAddress);
    return {
      chainId,
      poolAddress,
      isStaked: bg(liquidity).gt(0),
      bTokens: [{ isStaked: bg(liquidity).gt(0) }],
    };
  });
};

export const getLiquidityInfo = async (
  chainId,
  poolAddress,
  accountAddress
) => {
  return catchApiError(
    _getLiquidityInfo,
    [chainId, poolAddress, accountAddress],
  );
};

export const getEstimatedLiquidityInfo = async (
  chainId,
  poolAddress,
  accountAddress,
  newAmount,
) => {
  return catchApiError(async() => {
    [chainId, poolAddress, accountAddress] = checkApiInput(
      chainId,
      poolAddress,
      accountAddress
    );
    newAmount = checkAmount(newAmount)
    const res = ObjectCache.get(poolAddress, 'liquidity_info');
    if (res) {
      const {
        lTokenBalance,
        liquidity,
        shareValue,
        lTokenTotalSupply,
        totalDynamicEquity,
      } = res;
      const restShares = bg(lTokenBalance).plus(newAmount)
      // console.log('lTokenBalance', lTokenBalance.toString())
      return {
        percentage1: bg(restShares)
          .div(bg(lTokenTotalSupply).plus(newAmount))
          .toString(),
        shares1: restShares.toString(),
      };
    } else {
      throw AppError(ErrorCode.INVALID_CACHE, {
        name: `${poolAddress}.liquidity_info`,
        data: res,
      });
    }
  })
}
