import { AppError } from '../../shared/error/error';
import {
  catchTxApiError,
  getBlockInfo,
  naturalToDeri,
} from '../../shared/utils';
import {
  checkAddress,
  checkApiInput,
} from '../../shared/config'
import { normalizeChainId } from '../../shared/utils/validate';
import { perpetualPoolLiteDpmmFactory } from '../../v2_lite_dpmm/contract/factory';
import {
  getOracleFactoryChainlinkConfig,
  getPoolV2LiteManagerDpmmConfig,
} from '../config';
import {
  chainlinkFeedFactory,
  oracleFactoryChainlinkFactory,
  perpetualPoolLiteFactory,
  perpetualPoolLiteManagerDpmmFactory,
  perpetualPoolLiteManagerFactory,
} from '../factory';

// parameters: [symbolId, symbol, oracleAddress, multiplier, feeRatio, fundingRateCoefficient]
export const createPool = async (...args) => {
  return catchTxApiError(
    async (
      chainId,
      accountAddress,
      parameters,
      bTokenAddress,
      pairedTokenAddress
    ) => {
      chainId = normalizeChainId(chainId);
      const { address: poolManagerAddress } = getPoolV2LiteManagerDpmmConfig(
        chainId
      );
      const poolManager = perpetualPoolLiteManagerDpmmFactory(
        chainId,
        poolManagerAddress
      );
      // process parameters
      const newParameters = parameters.map((p) => naturalToDeri(p).toString());
      // send tx
      return await poolManager.createPool(
        accountAddress,
        newParameters,
        bTokenAddress,
        pairedTokenAddress
      );
    },
    args
  );
};

export const createMigrationPool = async (...args) => {
  return catchTxApiError(async (chainId, poolAddress, accountAddress) => {
    [chainId, poolAddress, accountAddress] = checkApiInput(
      chainId,
      poolAddress,
      accountAddress
    );
    const { address: poolManagerAddress } = getPoolV2LiteManagerDpmmConfig(
      chainId
    );
    const poolManager = perpetualPoolLiteManagerDpmmFactory(
      chainId,
      poolManagerAddress
    );
    // send tx
    return await poolManager.createMigrationPool(accountAddress, poolAddress);
  }, args);
};

// parameters: [symbolId, symbol, oracleAddress, multiplier, feeRatio, fundingRateCoefficient]
export const addSymbol = async (...args) => {
  return catchTxApiError(
    async (chainId, poolAddress, accountAddress, parameters) => {
      chainId = normalizeChainId(chainId);
      const perpetualPoolLite = perpetualPoolLiteDpmmFactory(
        chainId,
        poolAddress
      );
      // process parameters
      const newParameters = parameters.map((p, index) =>
        index > 2 ? naturalToDeri(p).toString() : p
      );
      // send tx
      return await perpetualPoolLite.addSymbol(accountAddress, newParameters);
    },
    args
  );
};

export const prepareMigration = async (
  chainId,
  poolAddress,
  accountAddress,
  targetPoolAddress
) => {
  return catchTxApiError(async () => {
    [chainId, poolAddress, accountAddress] = checkApiInput(
      chainId,
      poolAddress,
      accountAddress
    );
    chainId = normalizeChainId(chainId);
    const perpetualPoolLite = perpetualPoolLiteFactory(chainId, poolAddress);
    // send tx
    return await perpetualPoolLite.prepareMigration(
      accountAddress,
      targetPoolAddress,
      3
    );
  }, []);
};

export const approveMigration = async (
  chainId,
  poolAddress,
  accountAddress
) => {
  return catchTxApiError(async () => {
    [chainId, poolAddress, accountAddress] = checkApiInput(
      chainId,
      poolAddress,
      accountAddress
    );
    const perpetualPoolLite = perpetualPoolLiteDpmmFactory(
      chainId,
      poolAddress
    );
    // send tx
    return await perpetualPoolLite.approveMigration(accountAddress);
  }, []);
};

export const executeMigration = async (
  chainId,
  poolAddress,
  accountAddress,
  oldPoolAddress
) => {
  return catchTxApiError(async () => {
    [chainId, poolAddress, accountAddress] = checkApiInput(
      chainId,
      poolAddress,
      accountAddress
    );
    const perpetualPoolLite = perpetualPoolLiteFactory(chainId, oldPoolAddress);
    const lastUpdateBlock = await perpetualPoolLite.getLastUpdateBlock();
    const lastTimestamp = (await getBlockInfo(chainId, lastUpdateBlock))
      .timestamp;
    const perpetualPoolLiteDpmm = perpetualPoolLiteDpmmFactory(
      chainId,
      poolAddress
    );
    // send tx
    return await perpetualPoolLiteDpmm.executeMigrationWithTimestamp(
      accountAddress,
      oldPoolAddress,
      lastTimestamp
    );
  }, []);
};

export const createOracle = async (...args) => {
  return catchTxApiError(async (chainId, accountAddress, feedAddress) => {
    chainId = normalizeChainId(chainId);
    feedAddress = checkAddress(feedAddress);
    const feedContract = chainlinkFeedFactory(chainId, feedAddress);
    const symbol = await feedContract.symbol();
    if (typeof symbol === 'string' && symbol !== '') {
      const oracleFactoryConfig = getOracleFactoryChainlinkConfig(chainId);
      const oracleFactory = oracleFactoryChainlinkFactory(
        chainId,
        oracleFactoryConfig.address
      );
      // process parameters
      // send tx
      return await oracleFactory.createOracle(accountAddress, symbol);
    } else {
      throw new AppError('INVALID_SYMBOL', symbol);
    }
  }, args);
};
