import { observable, action, computed, makeObservable ,autorun} from "mobx";
import Oracle from "./Oracle";
import Position from "./Position";
import Contract from "./Contract";
import History from './History'
import Config from "./Config";
import { eqInNumber, storeConfig, getConfigFromStore, restoreChain, getFormatSymbol, getMarkpriceSymbol, getDefaultNw, percentTimesAmount, precision, firstLetterUppercase } from "../utils/utils";
import { getFundingRate, priceCache, DeriEnv, getVolatility, sortOptionSymbols } from "../lib/web3js/index";
import { bg } from "../lib/web3js/index";
import Intl from "./Intl";
import version from './Version'
import type from './Type'
import Type from "./Type";
import PoolManager from "./PoolManager";
import ApiProxy from "./ApiProxy";
import Pool from "./Pool";
import {numberWithCommas} from '../utils/utils'
import { OPTION, FUTURE, BUY,TRADE_ENTRY_VOLUME_STATUS, TRADE_ENTRY_DOLLAR_STATUS, TRADE_ENTRY_PERCENTAGE_STATUS, TRADE_ENTRY_SLIDER_STATUS, SELL, TRADING_APPEND_POSITION_STATUS, TRADING_CLOSE_POSITION_STATUS, TRADING_CREATE_POSITION_STATUS, SKIP_TRADE_CONFIRMATION, LONG, SHORT } from "../utils/Constants";

/**
 * 
 * 
 * 1. chain
 * 2. Oracle
 * 3. position
 * 4. contract
 * 5. history
 * 
 * 1. dynamic balance
 * 2. available blance
 * 
 * 1. chain change
 * 2. chains symbol changed
 * 3. index update
 * 4. volum change
 * 5. margin change
 * 
 * 1. dynamic balance
 * 2. margin
 * 3. available balance
 * 4. volume
 * 5. specs
 * 6. spec
 * 7. position
 * 8. contract
 * 9. fundingRate
 */

export default class Trading {
  version = null;
  wallet = null;
  type = null;
  index = ''
  markPrice = ''
  volume = ''
  priceDecimals = 2
  paused = false
  slideIncrementMargin = 0
  position = {}
  positions = []
  contract = {}
  fundingRate = {}
  volatility = ''
  history = []
  userSelectedDirection = 'long'
  supportChain = true
  optionsConfigs = {}
  pools = []
  pool = {}
  closeOnly = false
  inputPercentageValue = ''
  direction = ''
  entryStatus = TRADE_ENTRY_VOLUME_STATUS
  inputDollarValue = ''
  inputSliderValue = ''
  impactPreview = {} 
  symbols = [];
  symbolsWithMarkprice = []
  currentSymbol = {}
  loaded = false
  constructor() {
    makeObservable(this, {
      index: observable,
      markPrice: observable,
      volume: observable,
      inputPercentageValue: observable,
      slideIncrementMargin: observable,
      fundingRate: observable,
      volatility: observable,
      position: observable,
      positions: observable,
      history: observable,
      contract: observable,
      priceDecimals: observable,
      userSelectedDirection: observable,
      supportChain: observable,
      pools : observable,
      pool : observable,
      symbols : observable,
      symbolsWithMarkprice : observable,
      currentSymbol : observable,
      closeOnly : observable,
      direction : observable,
      entryStatus : observable,
      inputDollarValue : observable,
      inputSliderValue : observable,
      impactPreview : observable,
      loaded : observable,
      setWallet: action,
      setSymbols : action,
      setCurrentSymbol : action,
      setCloseOnly : action,
      setPriceDecimals: action,
      setIndex: action,
      setMarkPrice: action,
      setContract: action,
      setPosition: action,
      setPositions: action,
      setVolume: action,
      setUserSelectedDirection: action,
      setFundingRate: action,
      setVolatility: action,
      setHistory: action,
      setDirection : action,
      setEntryStatus : action,
      setInputDollarValue : action,
      setInputSliderValue : action,
      setImpactPreview : action,
      setSymbolsWithMarkprice : action,
      fundingRateTip: computed,
      initialMarginRatioTip: computed,
      maintenanceMarginRatioTip: computed,
      TransactionFeeTip: computed,
      fundingFeeValueTip: computed,
      fundingCoefficientTip: computed,
      rateTip: computed,
      multiplierTip: computed,
      TotalNetPositionTip: computed,
      isNegative: computed,
      isPositive: computed,
      dailyFunding : computed,
      fee : computed,
      maxEntryVolume : computed,
      minEntryVolume : computed,
      curTradeUnit : computed,
      minTradeScaleDecimal : computed,
      dollarValue : computed,
      maxAvailableMargin : computed,
      positionTradingStatus : computed,
      sliderValue : computed,
      volumeValue : computed,
      formatSliderValue : computed,
      percentageValue : computed,
      sliderTrackStyle : computed,
      accountInfo : computed,
      inputHelpText : computed,
      directionDisplay : computed,
      tradeWarn : computed,
      initialMarginRatio : computed,
      maintenanceMarginRatio : computed,
      curMarginUsage : computed,
      isAccountReady : computed,
      isTradingReady : computed,
      skipTradeConfirmation : computed,
      fundingFee : computed,
      fundingFeeRate :computed,
      fundingFeeTip : computed,
      symbolInfo : computed,
      symbolInfos : computed,
      directionChanged : computed,
      volumeChanged : computed,
      tradingNotional : computed,
    })
    this.indexOracle = new Oracle();
    this.markOracle = new Oracle();
    this.positionInfo = new Position()
    this.contractInfo = new Contract();
    this.historyInfo = new History();
    this.poolManager = new PoolManager();

    autorun(async () => {
      this.loadTransSummary();
      this.loadSymbolsWithprice();
    })

  }

  async loadTransSummary(){
    if(this.volume && this.symbolInfo) {
      const {chainId,account} = this.wallet.detail;
      const {symbolId,address} = this.symbolInfo
      let params = [chainId, address, Math.abs(this.volume), symbolId]
      const fee = await ApiProxy.request('getEstimatedFee',params)

      params = [chainId, address, account, Math.abs(this.volume),symbolId]
      const liqPrice = await ApiProxy.request('getEstimatedLiquidatePrice',params)
      params = [chainId, address, this.volume,symbolId]
      const markPriceAfter = await ApiProxy.request('getEstimatedTimePrice',params);


      // params = [chainId, address, symbolId]
      // const curLiqUsed = await ApiProxy.request('getLiquidityUsed',params)
      // params = [chainId, address, this.volume, symbolId]
      // const afterLiqUsed = await ApiProxy.request('getEstimatedLiquidityUsed',params);
      // params = [chainId, address, this.volume, symbolId]
      // const fundingAfter = await ApiProxy.request('getEstimatedFundingRate',params);
      const marginUsageAfter = this.positionTradingStatus === TRADING_CLOSE_POSITION_STATUS 
        ?
        //= marginUsage - current symbol margin usage + (current symbol margin usage - current volume margin).abs()
        bg(this.accountInfo.marginUsage).minus(this.accountInfo.curSymbolMarginUsage).plus(bg(this.accountInfo.curSymbolMarginUsage).minus(this.sliderValue).abs()).toNumber() 
        : 
        bg(this.accountInfo.marginUsage).plus(this.sliderValue).abs().toNumber()
      const impact = {
        fee : fee,
        tradePrice : markPriceAfter,
        availableMargin : this.accountInfo.availableMargin,
        availableMarginAfter : bg(this.accountInfo.dynEffMargin).minus(marginUsageAfter).toNumber(),
        marginUsage : this.accountInfo.marginUsage,
        marginUsageAfter : marginUsageAfter,
        liqPrice : this.position.liquidationPrice,
        liqPriceAfter : liqPrice
      }
      this.setImpactPreview(impact)
    }
  }

  async loadSymbolsWithprice(){
    const chainId = (this.wallet && this.wallet.detail && this.wallet.supportChain)? this.wallet.detail.id : getDefaultNw(DeriEnv.get()).id
    if(this.symbols && this.symbols.length > 0){
      const params = [chainId,this.pools.map(pool => pool.address)]
      let symbols = await ApiProxy.request('getPoolMarkPrices24hInfo',params)
      this.setSymbolsWithMarkprice(symbols)
    }
  }

  /**
   * 
   * wallet and version changed will init
   */
  async init(wallet, finishedCallback) {
    const isOption = Type.isOption
    const type = isOption ? OPTION : FUTURE
    const pools = await this.poolManager.loadByType(type);
    if (wallet.isConnected()) {
      this.setWallet(wallet);
      this.setPools(pools.filter(p => eqInNumber(wallet.detail.chainId,p.chainId)))
      const defaultPool = this.pools.length > 0 ? this.pools[0] : pools[0]
      this.setPool(defaultPool);
    } else if (!wallet.isConnected() || !wallet.supportWeb3()) {
      this.setPools(pools.filter(p => eqInNumber(getDefaultNw(DeriEnv.get()).id,p.chainId)))
      const defaultPool = this.pools.length > 0 ? this.pools[0] : pools[0]
      this.setPool(defaultPool)
    }
    this.setSymbols(this.pools.reduce((total,pool) => total.concat(pool.symbols),[]))
    //todo 
    const defaultSymbol = Type.isOption ? this.pool.symbols.find(s => s.symbol === 'BTCUSD-50000-C') : this.pool.symbols[0]
    this.setCurrentSymbol(defaultSymbol)
    this.loadBySymboInfo(this.wallet, this.currentSymbol, true, finishedCallback, isOption)
  }

  async onSymbolChange(selected, finishedCallback, isOption) {
    const pool = this.pools.find(pool => pool.address === selected.address)
    //v1 v2 symbolId
    const changed = version.isV1 
          ? selected.address !== this.pool.address 
          : (pool.address !== selected.address || selected.symbolId !== this.pool.symbolId)
    //symbol
    this.setCurrentSymbol(this.symbols.find(info => info.address === selected.address && info.symbolId === selected.symbolId))
    this.onChange(pool, changed, finishedCallback, isOption,this.currentSymbol)
  }

  async onChange(pool, changed, finishedCallback, isOption,currentSymbol) {
    if (pool) {
      this.clean();      
      this.setPool(pool)
      this.loadBySymboInfo(this.wallet, currentSymbol, changed, finishedCallback, isOption);
    } else {
      finishedCallback && finishedCallback()
    }
  }

  async loadBySymboInfo(wallet, currentSymbol, symbolChanged, finishedCallback, isOption) {
    //
    if (symbolChanged && currentSymbol) {
      this.indexOracle.addListener('indexPrice', data => {
        this.setIndex(data.close)
      })
      this.markOracle.addListener('markPrice',data => {
        this.setMarkPrice(data.close)
      })
    }
    if (currentSymbol) {
      Promise.all([
        this.positionInfo.load(wallet, this.currentSymbol, position => {
          this.setPosition(position)
          this.syncFundingRate(wallet, this.currentSymbol, isOption)
          type.isOption && this.syncVolatility(wallet, this.currentSymbol);
        }),
        this.contractInfo.load(wallet, this.currentSymbol, isOption),
        this.loadFundingRate(wallet, this.currentSymbol, isOption),
        this.positionInfo.loadAll(wallet, this.currentSymbol, positions => this.setPositions(positions)),
      ]).then(results => {
        if (results.length === 4) {
          if(results[0]) {
            this.setIndex(results[0].price)
            this.setMarkPrice(results[0].markPrice);
            this.setPosition(results[0]);
          }
          results[1] && this.setContract(results[1]);
          results[2] && this.setFundingRate(results[2]);
          results[3] && this.setPositions(results[3]);
        }
      }).finally(e => {
        finishedCallback && finishedCallback()
        this.currentSymbol.symbol && this.indexOracle.load(getFormatSymbol(this.currentSymbol.symbol.split('-')[0]))
        this.markOracle.load(getFormatSymbol(this.currentSymbol.markpriceSymbolFormat || `${this.currentSymbol.symbol}-MARKPRICE`))
        this.positionInfo.start()
        this.positionInfo.startAll();
        this.resume();
        this.setLoaded(true)
      })
    } else {
      finishedCallback && finishedCallback()
    }
    const histories = await this.historyInfo.load(wallet, currentSymbol, isOption)
    this.setHistory(histories);
  }

  refreshCache() {
    const currentSymbol  = this.currentSymbol;
    const symbol = type.isOption ? currentSymbol.symbol.split('-')[0] : currentSymbol.symbol
    priceCache.clear();
    priceCache.update(currentSymbol, symbol)
  }


  //session storage 
  getDefaultConfig(configs = [], wallet) {
    let defaultConfig = null;
    if (configs.length > 0) {
      const fromStore = this.getFromStore();
      if (fromStore && eqInNumber(wallet.detail.chainId, fromStore.chainId)) {
        defaultConfig = fromStore;
      }
      if (defaultConfig) {
        //config config
        const pos = configs.findIndex(c => c.pool === defaultConfig.pool);
        if (pos === -1) {
          defaultConfig = configs[0];
        }
      } else {
        defaultConfig = configs[0]
      }
    }
    return defaultConfig;
  }

  //
  store(config) {
    storeConfig(version.current, config)
  }

  getFromStore() {
    return getConfigFromStore(version.current)
  }

  async syncFundingRate() {
    //
    const fundingRate = await this.loadFundingRate(this.wallet, this.currentSymbol)
    if (fundingRate) {
      this.setFundingRate(fundingRate)
    }
  }

  async syncVolatility(wallet, symbolInfo) {
    const chainId = wallet && wallet.isConnected() ? wallet.detail.chainId : getDefaultNw(DeriEnv.get()).id
    if (symbolInfo) {
      const volatility = await ApiProxy.request('getVolatility',[chainId, symbolInfo.address, symbolInfo.symbolId]);
      this.setVolatility(volatility)
    }
  }

  async refresh() {
    this.pause()
    this.positionInfo.load(this.wallet, this.symbolInfo, position => {
      this.setPosition(position);
      this.syncFundingRate();
      type.isOption && this.syncVolatility(this.wallet, this.currentSymbol);
    });
    this.positionInfo.loadAll(this.wallet, this.symbolInfo, positions => this.setPositions(positions))
    this.syncFundingRate();
    type.isOption && this.syncVolatility(this.wallet, this.symbolInfo);
    this.wallet && this.wallet.isConnected() && this.wallet.loadWalletBalance(this.wallet.detail.chainId, this.wallet.detail.account)
    const history = await this.historyInfo.load(this.wallet, this.currentSymbol)
    if (history) {
      this.setHistory(history)
    }
    this.positionInfo.start();
    this.positionInfo.startAll();
    this.setInputDollarValue('')
    this.setInputSliderValue('')
    this.setInputPercentageValue('')
    this.setVolume('')
    this.setCloseOnly(false);
    this.resume();
  }

  /**
   * indexposition
   */
  pause() {
    this.setPaused(true)
    this.indexOracle.pause();
    this.markOracle.pause();
    this.positionInfo.pause();
  }

  /**
   * 
   */
  resume() {
    this.setPaused(false)
    this.indexOracle.resume();
    this.markOracle.resume();
    this.positionInfo.resume();
  }

  setLoaded(loaded){
    this.loaded = loaded
  }

  setWallet(wallet) {
    this.wallet = wallet;
  }

  setPools(pools){
    this.pools = pools;

  }

  setPool(pool){
    this.pool = pool
  }
  
  setSymbols(symbols){
    this.symbols = symbols;
  }

  setCurrentSymbol(symbol){
    this.currentSymbol = symbol;
  }

  setSymbolsWithMarkprice(symbols){
    this.symbolsWithMarkprice = symbols
  }

  setPriceDecimals(pool) {
    if (pool && pool.decimals) {
      this.priceDecimals = pool.decimals
    } else {
      this.priceDecimals = 2
    }
  }

  setIndex(index) {
    this.index = index;
  }

  setMarkPrice(markPrice) {
    this.markPrice = markPrice;
  }

  setPosition(position) {
    if (position) {
      this.position = position
    }
  }

  setPositions(positions) {
    if (positions) {
      this.positions = positions
    }
  }

  setContract(contract) {
    this.contract = contract
  }

  setHistory(history) {
    this.history = history
  }

  setFundingRate(fundingRate) {
    this.fundingRate = fundingRate;
  }

  setVolatility(volatility) {
    this.volatility = volatility
  }

  setVolume(volume) {
    this.setEntryStatus(TRADE_ENTRY_VOLUME_STATUS)
    this.volume = volume;
  }

  setInputPercentageValue(percentage){
    this.setEntryStatus(TRADE_ENTRY_PERCENTAGE_STATUS);
    this.inputPercentageValue = percentage
    if(percentage){
      const curNotional = percentTimesAmount(percentage,this.maxAvailableMargin);
      this.calculateVolume(curNotional)
    } 
  }

  setInputDollarValue(dollarValue){
    this.setEntryStatus(TRADE_ENTRY_DOLLAR_STATUS);
    this.inputDollarValue = dollarValue
    this.setInputPercentageValue('')
    this.calculateVolume(dollarValue)
  }

  setInputSliderValue(value){
    this.setEntryStatus(TRADE_ENTRY_SLIDER_STATUS);
    this.inputSliderValue = value
    this.calculateVolume(value);
    this.setInputPercentageValue('')
  }

  calculateVolume(value){
    setTimeout(() => {
      const volume = value ? bg(value).div(this.position.price).div(this.contract.initialMarginRatio).toFixed(this.minTradeScaleDecimal) : ''
      this.setVolume(volume)
    },0)
  }

  

  setPaused(paused) {
    this.paused = paused
  }

  setUserSelectedDirection(direction) {
    this.userSelectedDirection = direction
  }

  setCloseOnly(closeOnly){
    this.closeOnly = closeOnly
  }

  setDirection(direction){
    this.direction = direction
  }

  setEntryStatus(status){
    this.entryStatus = status
  }

  setImpactPreview(impact){
    this.impactPreview = impact
  }

  groupConfigBySymbol(symbols = []) {
    return symbols.reduce((total, config) => {
      const symbol = config.symbol.split('-')[0]
      if (!total[symbol]) {
        total[symbol] = []
      }
      config['underlier'] = symbol
      total[symbol].push(config)
      return total;
    }, [])
  }


  //
  async loadFundingRate(wallet, currentSymbol, isOption) {
    if (currentSymbol) {
      const chainId = wallet && wallet.isConnected() && wallet.isSupportChain(isOption) ? wallet.detail.chainId : getDefaultNw(DeriEnv.get()).id
      if (currentSymbol) {
        const res = await ApiProxy.request('getFundingRate',[chainId, currentSymbol.address, currentSymbol.symbolId]).catch(e => console.error('getFundingRate was error,maybe network is wrong'))
        return res;
      }
    }
  }


  doTransaction(){
    const volume = this.direction === SELL ? -this.volume : this.volume
    return ApiProxy.request('tradeWithMargin',[this.wallet.detail.chainId, this.currentSymbol.address, this.wallet.detail.account, volume, this.symbolInfo.symbolId],{includeResponse : true})
  }

  clean() {
    this.pause();
    this.indexOracle.clean();
    this.markOracle.clean();
    this.positionInfo.clean();
    this.version = null;
    this.pool = null;
    this.markPrice = ''
    this.index = ''
    this.volume = ''
    this.fundingRate = {}
    this.volatility = ''
    this.position = {}
    this.positions = []
    this.contract = {}
    this.history = []
  }


  get accountInfo(){
    const position = this.position;
    if(position && position.margin && position.unrealizedPnl && position.fundingFee ){
      const dynEffMargin = bg(position.margin).plus(position.unrealizedPnl).minus(position.fundingFee);
      return  {
        dynEffMargin : dynEffMargin.toString(),
        marginUsage : position.marginHeld,
        availableMargin : dynEffMargin.minus(position.marginHeld).lt(0) ? 0 : dynEffMargin.minus(position.marginHeld).toNumber() ,
        curSymbolMarginUsage : position.marginHeldBySymbol,
        curSymbolNotional : bg(position.volume).abs().times(this.contract.initialMarginRatio).times(position.price).toNumber(),
        marginUsagePercent : dynEffMargin.gt(0) ? bg(position.marginHeld).div(dynEffMargin).times(100).toFixed(0) : 0
      } 
    } 
    //dynEffMargin : 0,marginUsage : 0,availableMargin : 0,curSymbolMarginUsage : 0, curSymbolNotional : 0,marginUsagePercent : 0
    return {}
  }

  //
  get isPositive() {
    return bg(this.position.volume).gt(0);
  }

  //
  get isNegative() {
    return bg(this.position.volume).isNegative();
  }


  get dailyFunding(){
    return this.fundingRate.funding0 && this.markPrice && bg(this.fundingRate.funding0).div(bg(this.markPrice) ).times(bg(100)).toString()
  }

  get fee(){
    if(type.isFuture){
      return this.contract.feeRatio ? this.contract.feeRatio * 100 : 0 
    } else {
      if(this.contract.optionType === 'C'){
        if(this.contract.strike >= this.index) {
          return [Intl.get('trade', 'eo-mark-price') ,'*', this.contract.feeRatioOTM * 100]
        } else {
          return this.contract.underlier ? [`${this.contract.underlier} ${Intl.get('trade','price')}`,'*',this.contract.feeRatioITM * 100] : ['','']
        }
      } else {
        if(this.contract.strike < this.index) {
          return [Intl.get('trade', 'eo-mark-price') , '*',this.contract.feeRatioOTM * 100]
        } else {
          return this.contract.underlier ? [`${this.contract.underlier} ${Intl.get('trade','price')}`,'*',this.contract.feeRatioITM * 100] : ['','']
         } 
      }
    }
  }

  get fundingRateTip() {
    if (version && version.isV2) {
      if (this.fundingRate && this.fundingRate.fundingRatePerBlock && this.pool) {
        if (Intl.locale === 'zh') {
          return `${Intl.get('trade', 'funding-rate-per-block')} = ${this.fundingRate.fundingRatePerBlock}` +
            `<br> ${Intl.get('trade', 'per-block')} ${Intl.get('trade', '1-long-contract-pays-1-short-contract')} (${this.fundingRate.fundingRatePerBlock} * ${Intl.get('trade', 'index-price-camelize')} * ${this.contract.multiplier} ) ${this.pool.bTokenSymbol}`
        } else {
          return `${Intl.get('trade', 'funding-rate-per-block')} = ${this.fundingRate.fundingRatePerBlock}` +
            `<br>${Intl.get('trade', '1-long-contract-pays-1-short-contract')} (${this.fundingRate.fundingRatePerBlock} * ${Intl.get('trade', 'index-price-camelize')} * ${this.contract.multiplier} ) ${this.pool.bTokenSymbol} ${Intl.get('trade', 'per-block')}`
        }

      }
    } else {
      if (this.fundingRate && this.fundingRate.fundingRatePerBlock && this.pool) {
        if (Intl.locale === 'zh') {
          return `${Intl.get('trade', 'funding-rate-per-block')} = ${this.fundingRate.fundingRatePerBlock}` +
            `<br>${Intl.get('trade', 'per-block')} ${Intl.get('trade', '1-long-contract-pays-1-short-contract')} ${this.fundingRate.fundingRatePerBlock} ${this.pool.bTokenSymbol} `
        } else {
          return `${Intl.get('trade', 'funding-rate-per-block')} = ${this.fundingRate.fundingRatePerBlock}` +
            `<br>${Intl.get('trade', '1-long-contract-pays-1-short-contract')} ${this.fundingRate.fundingRatePerBlock} ${this.pool.bTokenSymbol} ${Intl.get('trade', 'per-block')})`
        }
      }
    }
    return ''
  }

  get fundingFeeValueTip() {
    if (this.fundingRate && this.fundingRate.fundingPerSecond && this.pool) {
      if (Intl.locale === 'zh') {
        return `${Intl.get('trade', 'funding-rate-per-second')} = ${this.fundingRate.fundingPerSecond}` +
          `<br> ${Intl.get('trade', 'per-second')} ${Intl.get('trade', '1-long-contract-pays-1-short-contract')} ${this.fundingRate.fundingPerSecond} ${this.pool.bTokenSymbol} `
      } else {
        return `${Intl.get('trade', 'funding-rate-per-second')} = ${this.fundingRate.fundingPerSecond}` +
          `<br>${Intl.get('trade', '1-long-contract-pays-1-short-contract')} ${this.fundingRate.fundingPerSecond} ${this.pool.bTokenSymbol} ${Intl.get('trade', 'per-second')}`
      }
    }
    return ''
  }

  get rateTip() {
    if (this.fundingRate && this.fundingRate.funding0 && this.markPrice) {
      return `${Intl.get('trade', 'rate-hover-one')} ${bg(this.fundingRate.funding0).div(bg(this.markPrice)).times(bg(100)).toString()}% ${Intl.get('trade', 'rate-hover-two')}`
    }
    return ''
  }

  get fundingCoefficientTip() {
    if (this.contract && this.contract.fundingRateCoefficient && this.symbolInfo.unit) {
      if (Intl.locale === 'zh') {
        return `1${this.symbolInfo.unit} ${Intl.get('trade', 'funding-coefficient-hove-one')} ${Intl.get('trade', 'funding-coefficient-hove-two')}${this.contract.fundingRateCoefficient} ${Intl.get('trade', 'funding-coefficient-hove-three')} ${bg(this.contract.fundingRateCoefficient).times(bg(86400)).toString()} ${Intl.get('trade', 'per-day')}
      `
      } else {
        return `${Intl.get('trade', 'funding-coefficient-hove-one')} ${this.symbolInfo.unit} ${Intl.get('trade', 'funding-coefficient-hove-two')} ${this.contract.fundingRateCoefficient} ${Intl.get('trade', 'funding-coefficient-hove-three')} ${bg(this.contract.fundingRateCoefficient).times(bg(86400)).toString()} ${Intl.get('trade', 'per-day')}
      `
      }

    }
    return ''
  }

  get initialMarginRatioTip() {
    if (this.contract && this.contract.initialMarginRatioOrigin) {
      return `${Intl.get('trade', 'in-the-money-initial')} = ${this.contract.initialMarginRatioOrigin}` +
        `<br>${Intl.get('trade', 'out-of-money-initial')} = ${Intl.get('trade', 'in-max')}(${this.contract.initialMarginRatioOrigin}${Intl.get('trade', 'otm-ratio-initial')} ${this.contract.initialMarginRatioOrigin / 10})` +
        `<br><br>${Intl.get('trade', 'otm-ratio-max')}`
    }
    return ''
  }
  get maintenanceMarginRatioTip() {
    if (this.contract && this.contract.maintenanceMarginRatioOrigin) {
      return `${Intl.get('trade', 'in-the-money-maintenance')} = ${this.contract.maintenanceMarginRatioOrigin}` +
        `<br>${Intl.get('trade', 'out-of-money-maintenance')} = ${Intl.get('trade', 'in-max')}(${this.contract.maintenanceMarginRatioOrigin}${Intl.get('trade', 'otm-ratio-maintenance')} ${this.contract.maintenanceMarginRatioOrigin / 10})` +
        `<br><br>${Intl.get('trade', 'otm-ratio-max')}`
    }
    return ''
  }

  get multiplierTip() {
    if (this.contract && this.pool) {
      return `${Intl.get('trade', 'the-notional-value-of')} ${this.contract.multiplier}${this.symbolInfo.unit}`
    }
    return ''
  }

  get TransactionFeeTip() {
    if (this.contract && (this.contract.feeRatioITM && this.contract.feeRatioOTM)) {
      return `${Intl.get('trade', 'transaction-fee-tip-in-the-money')} ${this.contract.underlier} ${Intl.get('trade', 'price')}  * ${bg(this.contract.feeRatioITM).times(bg(100)).toString()} %` +
        `<br> ${Intl.get('trade', 'transaction-fee-tip-out-of-money')} * ${bg(this.contract.feeRatioOTM).times(bg(100)).toString()} %`
    }
    return ''
  }
  get TotalNetPositionTip() {
    if (this.contract && this.fundingRate.tradersNetVolume && this.pool) {
      return `${Intl.get('trade', 'notional-of-total-net-position')} ${this.fundingRate.tradersNetVolume} ${this.pool.unit}`
    }
    return ''
  }

  /**
   * 
   * 
   * 1. closeOnly false
   *  max entry volume = dynamic effective margin / symbol's price (etc BTC) 
   * 2. closeOnly true
   *  max entry volume = current symbol margin held / symbol's price (etc BTC)
   */
  get maxEntryVolume(){
    return bg(this.maxAvailableMargin).div(this.position.price).div(this.contract.initialMarginRatio).toFixed(this.minTradeScaleDecimal);
  }


  //
  get minEntryVolume(){
    return this.contract && this.contract.multiplier;
  }

  get curTradeUnit(){
    return this.symbolInfo && this.symbolInfo.unit;
  }

  //
  get minTradeScaleDecimal(){
    return this.contract && precision(this.contract.multiplier)
  }

  get dollarValue(){
    return this.entryStatus !== TRADE_ENTRY_DOLLAR_STATUS && !this.inputDollarValue ? bg(this.volume).times(this.position.price).toFixed(2) : this.inputDollarValue
  }
  get sliderValue(){
    if(this.entryStatus === TRADE_ENTRY_SLIDER_STATUS) {
      return this.inputSliderValue
    } else {
      return  this.volume && this.position.price ? bg(this.volume).times(this.contract.initialMarginRatio).times(this.position.price).toFixed(2) : 0
    }
  }

  get tradingNotional(){
    // const volume = this.direction === SELL ? -this.volume : this.volume
    return this.volume && this.position.price && bg(this.volume).times(this.position.price).toFixed(2)
  }

  get volumeValue(){
    return this.entryStatus !== TRADE_ENTRY_VOLUME_STATUS ? bg(this.volume).toFixed(this.minTradeScaleDecimal) : this.volume
  }

  get percentageValue(){
    return `${this.volume && bg(this.volume).times(this.position.price).times(this.contract.initialMarginRatio).div(this.maxAvailableMargin).times(100)}%`
  }

  get formatSliderValue(){
    return this.sliderValue && bg(this.sliderValue).toFixed(this.minTradeScaleDecimal)
  }


  //slider bar css
  get sliderTrackStyle(){
    const buyColor = '#59AE99';
    const sellColor = '#CE5E5E';
    let startColor , endColor ;
    let startPercent ,endPercent;
    if(this.positionTradingStatus === TRADING_CLOSE_POSITION_STATUS){
      if(!this.closeOnly){
        //
        startPercent =  bg(this.accountInfo.availableMargin).div(this.maxAvailableMargin).times(100).toFixed(this.minTradeScaleDecimal)
        endPercent = bg(this.accountInfo.curSymbolMarginUsage).div(this.maxAvailableMargin).times(100).plus(startPercent).plus(7).toFixed(this.minTradeScaleDecimal)
        if(this.isPositive){
          startColor = sellColor;
          endColor = buyColor;
        } else {
          startColor = buyColor;
          endColor = sellColor
        }
        return {
          background : `linear-gradient(270deg, ${startColor} ${startPercent}%, ${endColor} ${endPercent}%, ${endColor} 100%`
        }
      } else {
        return {}
      }
    } else {
      return {}
    }
  }

  //
  get positionTradingStatus(){
    if((this.isPositive && this.direction === BUY) || (this.isNegative && this.direction === SELL)){
      return TRADING_APPEND_POSITION_STATUS;
    } else if((this.isPositive && this.direction === SELL) || (this.isNegative && this.direction === BUY)){
      return TRADING_CLOSE_POSITION_STATUS;
    } else {
      return TRADING_CREATE_POSITION_STATUS;
    }

  }

  get maxAvailableMargin(){
    if(this.accountInfo){
      if(this.positionTradingStatus === TRADING_CLOSE_POSITION_STATUS){
        if(this.closeOnly){
          return this.accountInfo.curSymbolNotional;
        } else {
          return bg(this.accountInfo.curSymbolMarginUsage).times(2).plus(this.accountInfo.availableMargin).toNumber()
        }
      }else {
        return bg(this.accountInfo.availableMargin).toNumber()
      }
    } else {
      return 0;
    }
  }

  //margin 
  get curMarginUsage(){
    return bg(this.volume).times(this.position.price).times(this.initialMarginRatio).toNumber();
  }

  get initialMarginRatio(){
    return this.contract && this.contract.initialMarginRatio;
  }

  get maintenanceMarginRatio(){
    return this.contract && this.contract.maintenanceMarginRatio
  }

  get directionDisplay(){
    if(this.direction === BUY){
      return 'long'
    } else {
      return 'short'
    }
  }

  get inputHelpText(){
    let openText = this.pool && this.symbolInfo && `${Intl.get('trade','open-a')} ${bg(this.volume).toFixed(this.minTradeScaleDecimal)} ${this.symbolInfo.unit} ${this.directionDisplay} ${this.symbolInfo.optionType} ${Intl.get('trade','position').toLowerCase()}`
    if(this.positionTradingStatus === TRADING_CLOSE_POSITION_STATUS){
      let percentage = bg(this.sliderValue).div(bg(this.maxAvailableMargin).toFixed(2)).times(100);
      percentage = percentage.gt(100) ? 100 : percentage.toFixed(0,0)
      const closeText = `${percentage}% ${Intl.get('trade','close-position')}`
      if(this.closeOnly){
        return closeText;
      } else {
        let percentage = bg(this.sliderValue).div(bg(this.accountInfo.curSymbolNotional).toFixed(2)).times(100)
        const closeText = `${percentage.gt(100) ? 100 : percentage.toFixed(0)}% ${Intl.get('trade','close-position')}`
        const amount = this.direction === BUY ? this.volume : -this.volume
        const volume =  bg(this.position.volume).plus(amount).abs()
        if(percentage.gt(100) && volume.gt(this.minEntryVolume)) {  
          openText = `${Intl.get('trade','open-a')} ${volume.toFixed(this.minTradeScaleDecimal)} ${this.symbolInfo.unit} ${this.directionDisplay} ${this.symbolInfo.optionType} ${Intl.get('trade','position').toLowerCase()}`
          return  `${closeText} + ${openText}` 
        } else {
          return closeText
        }
      }
    } else {
      return openText
    }
  }

  get tradeWarn(){
    return !this.closeOnly && this.maxAvailableMargin && !isNaN(this.volume) && bg(this.volumeValue).eq(bg(this.maxAvailableMargin).div(this.position.price).div(this.contract.initialMarginRatio).toFixed(this.minTradeScaleDecimal));
  }

  get isAccountReady(){
    return this.wallet && this.pool && this.pool.address
  }

  get isTradingReady(){
    return this.maxAvailableMargin > 0 && this.volume > 0
  }

  get skipTradeConfirmation(){
    return sessionStorage.getItem(SKIP_TRADE_CONFIRMATION) === '1'
  }

  get fundingFee(){
    if(this.fundingRate  && this.markPrice){
      return this.fundingRate.funding0
    }
    return '' 
  }

  get fundingFeeRate(){
    if(type.isFuture && this.fundingFee){
      return bg(this.fundingFee).div(this.markPrice).times(100).toString()
    }
    return ''
  }


  get fundingFeeTip (){
    if(type.isFuture){
      return Intl.get('trade','daily-funding-tip-future')
    } else {
      return Intl.get('trade','daily-funding-tip-option')
    }
  }

  mergeMarkpriceChange(symbol,symbolsWithMarkprice){
    const find = symbolsWithMarkprice.find(item => item.pool === symbol.address && item.symbolId === symbol.symbolId)
    if(find){
      const {volumn24h,markPrice,markPrice24h} = find
      symbol.volumn24h = volumn24h;
      symbol.markPrice = markPrice;
      symbol.markPrice24h = markPrice24h;
    } 
    return symbol;
  }

  get symbolInfos(){
    if(this.symbolsWithMarkprice && this.symbolsWithMarkprice.length){
      let symbols = this.symbols;
      symbols = symbols.map(s => this.mergeMarkpriceChange(s,this.symbolsWithMarkprice))
      //options 
      if(type.isOption){
        return this.groupConfigBySymbol(symbols);
      }
      return symbols;
    } else {
      if(type.isOption){
        return this.groupConfigBySymbol(this.symbols);
      }
      return this.symbols;
    }
  }


  get symbolInfo(){
    if(this.symbolsWithMarkprice && this.symbolsWithMarkprice.length && this.symbolsWithMarkprice.length.length > 0 && this.currentSymbol){
      return this.mergeMarkpriceChange(this.currentSymbol,this.symbolsWithMarkprice);
    } else {
      return this.currentSymbol || {};
    }
  }

  get directionChanged(){
    return {
      before : firstLetterUppercase(this.isPositive ? LONG : this.isNegative ? SHORT : Intl.get('trade','none')),
      now : firstLetterUppercase((+this.volumeChanged.nowValue) > 0 ? LONG : (+this.volumeChanged.nowValue) < 0 ? SHORT : ''  )
    }
  }

  get volumeChanged(){
    const curVolume = this.direction === BUY ? this.volume : -this.volume;
    const nowVolume = bg(curVolume).plus(this.position.volume ||0).toFixed(this.minTradeScaleDecimal)
    return {
      before : (+this.position.volume) === 0 ? '' : `${bg(this.position.volume).toFixed(this.minTradeScaleDecimal)} ${this.symbolInfo.unit}` ,
      nowValue : nowVolume,
      now : `${nowVolume} ${this.symbolInfo.unit}`,
      totalVolume : this.volume,
      notional : bg(nowVolume).times(this.position.price).toNumber()
    }
  }


}