
import { api as apiV1 } from '../v1/api'
import { api as apiV2Dpmm } from '../v2_dpmm/api'
import { api as apiV2l } from '../v2_lite/api'
import { api as apiV2lDpmm } from '../v2_lite_dpmm/api'
import { api as apiOption } from '../option/api'
import { VERSION_IDS_ALL } from '../shared/config'

export const api = (versionId) => {
  const apiMap = {
    v1: apiV1,
    v2_dpmm: apiV2Dpmm,
    v2_lite: apiV2l,
    v2_lite_open: apiV2l,
    v2_lite_dpmm: apiV2lDpmm,
    option: apiOption,
  };
  if (VERSION_IDS_ALL.includes(versionId)) {
    return apiMap[versionId];
  }
  return {}
};

export const defaultReturnValue = (value = {}) => {
  return {
    success: true,
    response: {
      data: value,
    },
    isDefaultReturnValue: true,
  };
};