import styled from "styled-components"
import Icon from "../Icon/Icon"

const Wraper = styled.div`
  display : flex;
  align-items : center;
  width: 288px;
  height: 71px;
  font-size : 14px;
  color : #E0ECFF;
  background: #203B60;
  box-sizing: border-box;
  box-shadow: 0px 11px 19px rgba(10, 23, 43, 0.25);
  border-radius: 8px;
  img {
    margin-left : 24px;
    margin-right : 16px;
  }
`
export default function MessageTemplate({style, options, message, close}){
  return (
    <Wraper style={style}>
      {options.type === 'success' && <Icon token='success' width={25} height={25}/>}
      {options.type === 'error'  && <Icon token='error' width={25} height={25}/>}
      {message}
    </Wraper>
  )
}