import React, { useState } from 'react'
import './header.less'
import logo from '../../assets/img/logo.svg'
import Menu from './Menu'
import { useRouteMatch } from 'react-router-dom';
import LangSelector from './LangSelector'
import NetworkSelector from './NetworkSelector'
import WalletSelector from './WalletSelector'
import { inject, observer } from 'mobx-react'
import useLang from '../../hooks/useLang'
import Icon from '../Icon/Icon'
import classNames from 'classnames'


function Header({ intl }) {
  const lang = useLang(intl.dict, 'header');
  const isIndex = useRouteMatch('/index');
  const isRoot = useRouteMatch({ path: '/', exact: true })
  const isTeam = useRouteMatch('/team');
  const isDownload = useRouteMatch('/logo')
  const [isShowSideMenu, setIsshowSideMenu] = useState(false)
  const showSideMenuClazz = classNames('header', { 'show-side-menu': isShowSideMenu })
  const notConnectWalletPage = isIndex || isTeam || isRoot || isDownload
  const showSideMenu = () => {
    setIsshowSideMenu(true)
  }
  return (
    <div className={showSideMenuClazz}>
      <div className='left'>
        <div className='small-menu' onClick={showSideMenu}><Icon token='menu' /></div>
        <div className='logo'>
          <a href='https://deri.finance/#index'>
            <img src={logo} alt='logo' />
            <span className='name' >DERI</span>
          </a>
        </div>
        <Menu lang={lang} closeSideMenu={() => setIsshowSideMenu(false)} />
      </div>
      <div className='right'>
        <LangSelector />
        {!notConnectWalletPage && <>
          <NetworkSelector />
          <WalletSelector lang={lang} />
        </>}
      </div>
    </div>
  )
}
export default inject('intl')(observer(Header))