import { observable, action, computed, makeObservable } from "mobx";
import { OPTION, VERSION_V2_OPEN, VERSION_V2, VERSION_V2_LITE, CALL_OPTION, PUT_OPTION } from '../utils/Constants'
import apiProxy from './ApiProxy'
import { isCakeLP, isSushiLP, getMarkpriceSymbol } from '../utils/utils'
import {bg} from '../lib/web3js/index'
import {sortOptionSymbols } from "../lib/web3js/index";

const columnMap = {
  pool: 'address'
}
export default class Pool {
  chainId = ''
  chain = ''
  zone = ''
  address = ''
  version = ''
  type = ''
  isLp = ''
  bTokenSymbol = ''
  symbols = []
  bTokens = []
  decimals = 2
  markpriceSymbolFormat = ''
  currentSymbol  = null

  constructor(data) {
    makeObservable(this, {
      chainId: observable,
      chain: observable,
      zone: observable,
      address: observable,
      symbols: observable,
      bTokens: observable,
      bTokenSymbol:observable,
      isLp: observable,
      decimals : observable,
      markpriceSymbolFormat : observable,
      currentSymbol : observable,
      setCurrentSymbol : action,
      setMarkpriceSymbolFormat : action,
      setAddress : action,
      setChain : action,
      setChainId : action,
      setZone : action,
      setVersion : action,
      setType : action,
      setSymbols : action,
      setBTokens : action,
      setDecimals : action,
      selectedSymbol : computed,
      isFuture : computed,
      isOption : computed,
      isInno : computed,
      isOpen : computed
    })
    this.init(data)
  }

  init(data = {}) {
    const keys = Object.keys(data);
    for (const k in keys) {
      const name = columnMap[keys[k]] ? columnMap[keys[k]] : keys[k];
      if (this.hasOwnProperty(name) && data[keys[k]]) {
        const attr = name.replace(/^\S/, s => s.toUpperCase())
        const method = `set${attr}`        
        if(this[method]){
          this[method].call(this,data[keys[k]])
        } else {
          this[name] = data[keys[k]]
        }
      }
    }
  }

  async getPoolInfo(wallet, pool) {
    let params = [pool.chainId, pool.address]
    let apyInfo = await apiProxy.request('getPoolInfoApy', params);
    let liq = await apiProxy.request('getPoolLiquidity', params);
    const detail = {
      liquidity: liq.totalValue || liq.totalLiquidity,
      apy: ((+apyInfo.maxApy) * 100).toFixed(2),
      bTokensApy: apyInfo.bTokens,
      bTokensLiq:liq.bTokens,
    }
    if (pool.isLp) {
      let lpApy = await apiProxy.request('getLpPoolInfoApy', params)
      const apy = ((+lpApy.apy2) * 100).toFixed(2);
      detail['lpApy'] = apy
      detail['isCakeLP'] = isCakeLP(pool.address)
      detail['isSushiLP'] = isSushiLP(pool.address)
    }
    return detail
  }

  async getIsStaked(wallet,pool){
    let params = [pool.chainId,pool.address,wallet.detail.account]
    let bool = await apiProxy.request('getUserStakeInfo',params)
    return bool
  }

  async getLiquidityInfo(wallet, address, version, isLp, bTokenId) {
    let params = [wallet.detail.chainId, address, wallet.detail.account, bTokenId]
    let info = null;
    let res;
    if (isLp) {
      res = await apiProxy.request('getLpLiquidityInfo', params)
    } else {
      res = await apiProxy.request('getLiquidityInfo', params)
    }
    const shares = res.shares ? bg(res.shares) : bg(0)
    if (['clp','slp','v1','v2_lite','v2_lite_open','option'].includes(version)) {
      const total = shares.isNaN() ? bg(0) : shares.multipliedBy(res.shareValue) 
      info = {
        total: res.poolLiquidity,
        shareValue: res.shareValue,
        percent: res.poolLiquidity > 0 ? total.dividedBy(res.poolLiquidity).multipliedBy(100).toFixed(2) : 0,
        shares: shares.toFixed(2),
        formatShares: shares.toFixed(2),
        totalShares: bg(shares).toString(),
        values: total.toFixed(4),
      }
    } else {
      info = {
        total: res.poolLiquidity,
        pnl: (+res.pnl).toFixed(2),
        swappedPnl: (+res.swappedPnl).toFixed(2),
        percent: res.poolLiquidity > 0 ? shares.dividedBy(res.poolLiquidity).multipliedBy(100).toFixed(2) : 0,
        shares: shares.toFixed(2),
        formatShares: shares.toFixed(2),
        totalShares: bg(shares).toString(),
      }
    }
    return info
  }

  setChainId(chainId) {
    this.chainId = chainId;
  }

  setChain(chain) {
    this.chain = chain
  }

  setZone(zone) {
    this.zone = zone;
  }

  setAddress(address) {
    this.address = address;
  }

  setType(type) {
    this.type = type
  }

  setSymbols(symbols) {
    symbols = symbols.map(s => {
      const tokenInfo = this.bTokens.length > 0 ? this.bTokens[0] : {}
      s.symbolDisplay = this.isOpen || this.isInno ? `${s.symbol}/${tokenInfo.bTokenSymbol}` : s.symbol
      s.type = this.type
      s.address = this.address
      s.version = this.version
      s.bTokenSymbol = tokenInfo.bTokenSymbol
      s.chain = /bsctestnet/.test(this.chain) ? 'bsc' : this.chain
      s.zone = this.zone
      s.optionType = ''
      if(s.type === OPTION){
        s.optionType = /c$/i.test(s.symbol) ? CALL_OPTION : PUT_OPTION
      }
      if (!this.isOpen) {
        s.markpriceSymbolFormat = getMarkpriceSymbol(s)
      } 
      return s;
    })
    if(this.type === OPTION){
      this.symbols = sortOptionSymbols(symbols)
    } else {
      this.symbols = symbols
    }
  }


  setCurrentSymbol(symbol){
    this.currentSymbol = symbol
  }


  setMarkpriceSymbolFormat(markpriceSymbolFormat){
    this.markpriceSymbolFormat = markpriceSymbolFormat
  }

  setDecimals(decimals){
    this.decimals = decimals
  }


  setVersion(version){
    this.version = version
  }

  setBTokens(bTokens){
    this.bTokens = bTokens;
  }

  get selectedSymbol(){
    if(this.currentSymbol){
      return this.currentSymbol
    } else {
      return this.symbols.length > 0 ? this.symbols[0] : {}
    }
  }

  get isFuture(){
    return this.type === VERSION_V2;
  }

  get isOption(){
    return this.type === OPTION;
  }

  get isInno(){
    return this.version === VERSION_V2_LITE
  }

  get isOpen(){
    return this.version === VERSION_V2_OPEN
  }

  
}