import React, { useState, useEffect } from 'react'
import styled from 'styled-components';
import bscLogoIcon from '../../assets/img/bsc-logo.svg'
import arrowDownIcon from '../../assets/img/arrow-down.svg'
import Tooltip from '../Tooltip/Tooltip';
import { getNetworkList, getDefaultNw } from '../../utils/utils';
import { DeriEnv } from '../../lib/web3js';
import { inject, observer } from 'mobx-react';
import Icon from '../Icon/Icon';
const Wrapper = styled.div`
  display : flex;
  align-items : center;
  color : #93A1B8;
  width : 108px;
  margin-left : 16px;
  cursor : pointer;
  .name {
    margin-left : 4px
  }
  .cur-network {
    display : flex;
    align-items : center;
  }
  #network-select {
    .item {
      padding : 16px;
      color: #93A1B8;
      display: flex;
      align-items: center;
      img {
        margin-right : 8px;
      }
    }
    .item:hover,.item.selected {
      color : #E0ECFF;
      cursor : pointer;
    }
  }
`  
function NetworkSelector({wallet}){
  const [networkList, setNetworkList] = useState([])
  const [nowIcon,setNowIcon] = useState('BNBUSD')
  const calculatePosition = (position,
    currentEvent,
    currentTarget,
    refNode,
    place,
    desiredPlace,
    effect,
    offset) => {
      const rect = currentTarget.getBoundingClientRect();
      const top = rect.top + rect.height + 12;
      const left = (rect.left - refNode.offsetWidth + rect.width)
      return {top:top + offset.top,left : rect.left}
    }

  const onSelect = (network) => {
    wallet.switchNetwork(network)
  }

  useEffect(() => {
    const networkList = getNetworkList(DeriEnv.get());
    const defaultNw = getDefaultNw(DeriEnv.get());
    if(wallet.isConnected()){
    let icon = networkList.filter(p => (+p.id) === (+wallet.detail.chainId))
    if(icon.length){
      icon = icon[0].icon
      setNowIcon(icon)
    }
    }
    wallet.setDefaultNw(defaultNw)
    setNetworkList(networkList)
  }, [wallet,wallet.detail])


  return (
    <Wrapper className='network-select'>
      <div className='cur-network' data-tip data-for='network-select' data-event='click'>
        <Icon token={nowIcon} width='20'/>
        <div className='name'>{wallet.detail.name}</div>
        <Icon token='arrow-down'/>
      </div>
      <Tooltip  id='network-select' width = {180} offset={{top : 14}}  overridePosition={calculatePosition} type="info" clickable >
      {networkList.map((network,index) => (
        <div onClick = {()=> onSelect(network)} key={index} className={`item ${wallet.detail.code && network.code === wallet.detail.code ? 'selected' : network.isDefault ? 'selected' : ''}`} >
          <Icon token={network.icon} width='24'/><span>{network.name}</span>
        </div>)
      )}
      </Tooltip>
    </Wrapper>
  )
}
export default inject('wallet')(observer(NetworkSelector))