import { ContractBase } from '../../shared/contract';
import {
  deleteIndexedKey,
  fromWeiForObject,
  fromWei,
  toWei,
  naturalToDeri,
  getBlockInfo,
  deriToNatural,
  bg,
  max,
 } from '../../shared/utils';
import { getVolatilitySymbols, normalizeOptionSymbol } from '../../shared/config/token';
import { getPoolViewerConfig } from '../../shared/config/pool';
import { chainlinkOracleFactory, ERC20Factory
 } from '../../shared/contract/factory';
import { getOracleVolatilitiesForOption } from '../../shared/utils/oracle';
import {
  everlastingOptionViewerFactory,
  lTokenOptionFactory,
  optionPricerFactory,
  pTokenOptionFactory,
} from '../factory/tokens';
import { volatilitiesCache } from '../utils';
import { everlastingOptionAbi } from './abis.js';

const SECONDS_IN_A_DAY = 60 * 60 * 24
const _calculateDpmmPrice = (
  indexPrice,
  theoreticalPrice,
  delta,
  alpha,
  tradersNetPosition,
  liquidity
) => {
  let K = "0", dpmmPrice = "0";
  if (bg(liquidity).gt(0)) {
    K = bg(indexPrice)
      .times(indexPrice)
      .div(theoreticalPrice)
      .times(bg(delta).abs())
      .times(alpha)
      .div(liquidity);
  } 
  dpmmPrice = theoreticalPrice.times(
    bg(1).plus(bg(K).times(tradersNetPosition))
  );
  return [K, dpmmPrice];
};

const _queryTradeDpmm = (
  tradersNetPosition,
  theoreticalPrice,
  tradePosition,
  K
) => {
  const r = bg(tradersNetPosition)
    .plus(tradePosition)
    .pow(2)
    .minus(bg(tradersNetPosition).pow(2))
    .times(K)
    .div(2)
    .plus(bg(tradePosition));
  return bg(theoreticalPrice).times(r);
};

export class EverlastingOption extends ContractBase {
  // init
  constructor(chainId, contractAddress) {
    super(chainId, contractAddress, everlastingOptionAbi);

    // this.config = getPoolConfig(
    //   contractAddress,
    //   undefined,
    //   undefined,
    //   'option'
    // );
    // this.offchainSymbolIds = this.config.offchainSymbolIds;
    // this.offchainSymbols = this.config.offchainSymbols;
    // this.volatilitySymbols = this.config.volatilitySymbols;
    // this.bTokenAddress = this.config.bToken;
    // this.lTokenAddress = this.config.lToken;
    // this.pTokenAddress = this.config.pToken;
    this.viewerAddress = getPoolViewerConfig(this.chainId, 'option');
  }
  async init(usedByEstimatedApi = false) {
    await this._init();
    if (!this.addresses || !this.pToken) {
      [this.addresses, this.parameters] = await Promise.all([
        this.getAddresses(),
        this.getParameters(),
      ]);
      //console.log(this.addresses, this.parameters, this.state)
      const {
        pTokenAddress,
        lTokenAddress,
        optionPricerAddress,
        bTokenAddress,
      } = this.addresses;
      this.lToken = lTokenOptionFactory(this.chainId, lTokenAddress);
      this.pToken = pTokenOptionFactory(this.chainId, pTokenAddress);
      this.pricer = optionPricerFactory(this.chainId, optionPricerAddress);
      this.bToken = ERC20Factory(this.chainId, bTokenAddress);
      this.viewer = everlastingOptionViewerFactory(
        this.chainId,
        this.viewerAddress
      );
      this.bTokenSymbol = await this.bToken.symbol();
      this.fundingPeriod = bg(1).div(365).toString();
      this.fundingCoefficient = bg(1).div(24).div(3600).toString();
    }
    if (!usedByEstimatedApi) {
      const symbolIds = await this.pToken.getActiveSymbolIds();
      if (
        !this.activeSymbolIds ||
        this.activeSymbolIds.toString() !== symbolIds.toString()
      ) {
        this.activeSymbolIds = symbolIds;

        this.symbols = await Promise.all(
          this.activeSymbolIds.reduce(
            (acc, symbolId) => [...acc, this.getSymbol(symbolId)],
            []
          )
        );
        this.activeSymbolNames = this.symbols.map((s) => s.symbol);
        const ttt = await volatilitiesCache.get(this.activeSymbolNames)
        const symbolVolatilities = ttt.map((v) => v.volatility);
        const state = await this.viewer.getPoolStates(
          this.contractAddress,
          [],
          symbolVolatilities
        );
        const { symbolState } = state;

        // update state
        //this.activeSymbolIds = activeSymbolIds;
        this.activeSymbols = symbolState.filter((s) =>
          this.activeSymbolIds.includes(s.symbolId)
        );
        // for tx use
        this.volatilitySymbols = getVolatilitySymbols(
          this.symbols.map((s) => s.symbol)
        );
      }
      this.state = await this.getPoolStateValues();
      this.state.preTimestamp = this.state.lastTimestamp;
      this.state.curTimestamp = Math.floor(Date.now() / 1000).toString();
      this.userState = {}
    }
  }

  // query
  // async OptionPricer() {
  //   const res = await this._call('OptionPricer', []);
  //   return res;
  // }
  // async PmmPricer() {
  //   const res = await this._call('PmmPricer', []);
  //   return res;
  // }
  // async _T() {
  //   const res = await this._call('_T', []);
  //   return fromWei(res);
  // }
  async getAddresses() {
    const res = await this._call('getAddresses', []);
    return deleteIndexedKey(res);
  }
  async getLastTimestamp() {
    const res = await this._call('getPoolStateValues', []);
    return res[1];
  }
  async getLiquidity() {
    const res = await this._call('getPoolStateValues', []);
    return fromWei(res[0]);
  }
  async getPoolStateValues() {
    const res = await this._call('getPoolStateValues', []);
    return {
      liquidity: fromWei(res[0]),
      lastTimestamp: res[1],
      protocolFeeAccrued: fromWei(res[2]),
    };
  }
  async getParameters() {
    const res = await this._call('getParameters', []);
    return fromWeiForObject(deleteIndexedKey(res), [
      'initialMarginRatio',
      'liquidationCutRatio',
      'maintenanceMarginRatio',
      'maxLiquidationReward',
      'minLiquidationReward',
      'minPoolMarginRatio',
      'protocolFeeCollectRatio',
    ]);
  }
  // async getProtocolFeeAccrued() {
  //   const res = await this._call('getPoolStateValues', []);
  //   return fromWei(res[2])
  // }

  async getSymbol(symbolId) {
    const res = await this._call('getSymbol', [symbolId]);
    return fromWeiForObject(deleteIndexedKey(res), [
      'strikePrice',
      'multiplier',
      'feeRatioITM',
      'feeRatioOTM',
      'alpha',
      'tradersNetVolume',
      'tradersNetCost',
      'cumulativeFundingRate',
    ]);
  }
  async getSymbols(volatilities = []) {
    const symbols = await Promise.all(
      this.activeSymbolIds.reduce(
        (acc, i) => acc.concat([this.getSymbol(i)]),
        []
      )
    );
    const symbolIndexPrices = await Promise.all(
      symbols.reduce(
        (acc, s) => [
          ...acc,
          chainlinkOracleFactory(this.chainId, s.oracleAddress).getPrice(),
        ],
        []
      )
    );
    if (volatilities.length !== this.activeSymbolIds.length) {
      // volatilities = await Promise.all(
      //   symbols.reduce(
      //     (acc, s) => [
      //       ...acc,
      //       offChainVolatilityOracleFactory(
      //         this.chainId,
      //         s.volatilityAddress
      //       ).volatility(),
      //     ],
      //     []
      //   )
      // );
      volatilities = (
        await volatilitiesCache.get(this.activeSymbolNames)
      ).map((v) => deriToNatural(v.volatility).toString());
    }
    const timeValues = await Promise.all(
      symbols.reduce(
        (acc, s, index) => [
          ...acc,
          this.pricer.getEverlastingTimeValueAndDelta(
            toWei(symbolIndexPrices[index]),
            toWei(s.strikePrice),
            toWei(volatilities[index]),
            toWei(this.fundingPeriod)
          ),
        ],
        []
      )
    );
    let dynamicEquity = this.state.liquidity;
    for (let i = 0; i < symbols.length; i++) {
      const s = symbols[i];
      s.tradersNetPosition = bg(s.tradersNetVolume)
        .times(s.multiplier)
        .toString();
      s.indexPrice = symbolIndexPrices[i];
      s.volatility = volatilities[i];
      s.intrinsicValue = s.isCall
        ? max(bg(s.indexPrice).minus(s.strikePrice), 0).toString()
        : max(bg(s.strikePrice).minus(s.indexPrice), 0).toString();
      s.timeValue = timeValues[i].timeValue;
      s.theoreticalPrice = bg(s.intrinsicValue).plus(s.timeValue).toString();
      s.delta = timeValues[i].delta;
      if (bg(s.intrinsicValue).gt(0)) {
        if (s.isCall) {
          s.delta = bg(s.delta).plus(1).toString();
        } else {
          s.delta = bg(s.delta).minus(1).toString();
        }
      } else if (bg(s.indexPrice).eq(s.strikePrice)) {
        s.delta = bg(1).div(2).toString();
      }
      const [K, dpmmPrice] = _calculateDpmmPrice(
        s.indexPrice,
        bg(s.intrinsicValue).plus(s.timeValue),
        s.delta,
        s.alpha,
        bg(s.tradersNetVolume).times(s.multiplier),
        this.state.liquidity
      );
      s.K = K.toString();
      s.dpmmPrice = dpmmPrice.toString();
      const cost = _queryTradeDpmm(
        s.tradersNetPosition,
        s.theoreticalPrice,
        bg(s.tradersNetPosition).negated().toString(),
        s.K
      ).negated();
      s.pnl = cost.minus(s.tradersNetCost).toString();
      dynamicEquity = bg(dynamicEquity).minus(s.pnl).toString();
      s.fundingPerSecond = bg(s.dpmmPrice)
        .minus(s.intrinsicValue)
        .times(s.multiplier)
        .times(this.fundingCoefficient)
        .toString();
      s.fundingPerVolume = bg(s.fundingPerSecond).times(SECONDS_IN_A_DAY).toString()
      s.funding = bg(s.fundingPerVolume).div(s.multiplier).toString()
      const offset2 = bg(s.fundingPerSecond).times(
        bg(this.state.curTimestamp).minus(this.state.preTimestamp)
      );
      // console.log('offset2', offset2.toString())
      s.cumulativeFundingRate = bg(s.cumulativeFundingRate).plus(offset2).toString()
    }

    this.state.dynamicEquity = dynamicEquity;
    this.symbols = symbols

    return symbols;
  }

  async getPositions(accountAddress) {
    const positions = await Promise.all(
      this.activeSymbolIds.reduce(
        (acc, symbolId) => [...acc, this.pToken.getPosition(accountAddress, symbolId)],
        []
      )
    );
    let totalPnl = '0', totalFundingAccrued = '0'
    for (let i = 0; i < positions.length; i++) {
      // const cost = bg(positions[i].volume).times(this.symbols[i].dpmmPrice).times(this.symbols[i].multiplier).toString()
      const cost = _queryTradeDpmm(
        this.symbols[i].tradersNetPosition,
        this.symbols[i].theoreticalPrice,
        bg(positions[i].volume)
          .times(this.symbols[i].multiplier)
          .negated()
          .toString(),
        this.symbols[i].K
      )
        .negated()
        .toString();
      positions[i].pnl = bg(cost).minus(positions[i].cost).toString();
      positions[i].fundingAccrued = bg(positions[i].volume)
        .times(
          bg(this.symbols[i].cumulativeFundingRate).minus(
            positions[i].lastCumulativePremiumFundingRate
          )
        )
        .toString();
      totalPnl = bg(totalPnl).plus(positions[i].pnl).toString()
      totalFundingAccrued = bg(totalFundingAccrued).plus(positions[i].fundingAccrued).toString()
    }
    this.userState.totalPnl = totalPnl
    this.userState.totalFundingAccrued = totalFundingAccrued
    this.positions = positions

    return positions
  }

  // tx
  async _getVolSymbolPrices() {
    await this._init();
    let volatilities = [];
    let oracleSymbols = [];
    if (this.volatilitySymbols.length > 0) {
      const volatilityInfos = await getOracleVolatilitiesForOption(
        this.activeSymbols.map((s) => s.symbol)
      );
      volatilities = Object.values(volatilityInfos).reduce((acc, p, index) => {
        const oracleSymbol = normalizeOptionSymbol(
          this.activeSymbolNames[index]
        );
        if (!oracleSymbols.includes(oracleSymbol)) {
          oracleSymbols.push(oracleSymbol);
          acc.push([
            this.activeSymbolIds[index],
            p.timestamp,
            p.volatility,
            parseInt(p.v).toString(),
            p.r,
            p.s,
          ]);
        }
        return acc;
      }, []);
    }
    //console.log('prices', prices);
    return volatilities;
  }

  async addLiquidity(accountAddress, bAmount) {
    const prices = await this._getVolSymbolPrices();
    return await this._transact(
      'addLiquidity',
      [naturalToDeri(bAmount), prices],
      accountAddress
    );
  }
  async removeLiquidity(accountAddress, lShares) {
    const prices = await this._getVolSymbolPrices();
    let amount = naturalToDeri(lShares);
    return await this._transact(
      'removeLiquidity',
      [amount, prices],
      accountAddress
    );
  }
  async addMargin(accountAddress, bAmount) {
    return await this._transact(
      'addMargin',
      [naturalToDeri(bAmount)],
      accountAddress
    );
  }
  async removeMargin(accountAddress, bAmount) {
    const prices = await this._getVolSymbolPrices();
    return await this._transact(
      'removeMargin',
      [naturalToDeri(bAmount), prices],
      accountAddress
    );
  }
  async trade(accountAddress, symbolId, tradeVolume) {
    const prices = await this._getVolSymbolPrices();
    return await this._transact(
      'trade',
      [symbolId, naturalToDeri(tradeVolume), prices],
      accountAddress
    );
  }

  // trade history
  async formatTradeEvent(event) {
    const info = event.returnValues;
    const tradeVolume = deriToNatural(info.tradeVolume).toString();
    const block = await getBlockInfo(this.chainId, event.blockNumber);
    const symbolId = info.symbolId;
    const indexPrice = deriToNatural(info.indexPrice).toString();
    const index = this.activeSymbolIds.indexOf(symbolId);
    if (index > -1) {
      const symbol = this.symbols[index];
      const tradeFee = info.tradeFee;

      const direction =
        tradeFee !== '-1'
          ? bg(tradeVolume).gt(0)
            ? 'LONG'
            : 'SHORT'
          : 'LIQUIDATION';
      const price = bg(info.tradeCost)
        .div(info.tradeVolume)
        .div(symbol.multiplier)
        .toString();
      const notional = bg(tradeVolume)
        .abs()
        .times(indexPrice)
        .times(symbol.multiplier)
        .toString();
      const contractValue = bg(tradeVolume)
        .abs()
        .times(price)
        .times(symbol.multiplier)
        .toString();

      const res = {
        symbolId: info.symbolId,
        symbol: symbol.symbol,
        trader: info.trader,
        direction,
        volume: bg(tradeVolume).abs().toString(),
        price,
        indexPrice,
        notional,
        contractValue,
        transactionFee:
          tradeFee === '-1' ? '0' : deriToNatural(tradeFee).toString(),
        transactionHash: event.transactionHash,
        time: block.timestamp * 1000,
        extra: {},
      };
      //console.log(JSON.stringify(res));
      return res;
    } else {
      return null;
    }
  }
}
