import { ErrorCode, ErrorMessage, ErrorName } from './error_code';

export class AppError extends Error {
  constructor(code, data = {}) {
    if (Object.values(ErrorCode).includes(code)) {
      const fullMessage = `${ErrorName[code]}: ${ErrorMessage[code](data)}`;
      super(fullMessage);
      this.code = code;
      this.name = ErrorName[code];
      this.message = fullMessage;
      this.data = data;
    } else {
      throw new Error(`invalid error code '${code}'`);
    }
  }
  toString() {
    return this.message;
  }
}
