import {
  getSlpLiquidityInfo,
  getSlpWalletBalance,
  isSlpUnlocked,
} from './slp_pool_api';
import {
  getClp2LiquidityInfo,
  getClp2WalletBalance,
  isClp2Unlocked,
} from './clp2_pool_api';
// import {
//   getClpLiquidityInfo,
//   getClpWalletBalance,
//   isClpUnlocked,
// } from './clp_pool_api';
import { getLpConfig } from '../config';
import { clp2PoolFactory, slpPoolFactory } from '../factory/contract';
import { bg, catchApiError } from '../utils';

export const getLpUserStakeInfo = async (
  chainId,
  poolAddress,
  accountAddress
) => {
  return catchApiError(async () => {
    const { version } = await getLpConfig(chainId, poolAddress);
    let liquidity;
    if (version === 'slp') {
      const slp = slpPoolFactory(chainId, poolAddress);
      liquidity = await slp.getLiquidity(accountAddress);
      //console.log('slp', liquidity.toString())
    } else if (version === 'clp') {
      const clp = clp2PoolFactory(chainId, poolAddress);
      liquidity = await clp.getLiquidity(accountAddress);
      //console.log('clp', liquidity.toString())
    }
    return { chainId, poolAddress, isStaked: bg(liquidity).gt(0) };
  });
};

export const getLpLiquidityInfo = async (
  chainId,
  poolAddress,
  accountAddress
) => {
  const { version } = await getLpConfig(chainId, poolAddress);
  if (version === 'slp') {
    return await getSlpLiquidityInfo(chainId, poolAddress, accountAddress);
  // } else if (type === 'clp') {
  //   return await getClpLiquidityInfo(chainId, poolAddress, accountAddress);
  } else if (version === 'clp') {
    return await getClp2LiquidityInfo(chainId, poolAddress, accountAddress);
  }
};

export const isLpUnlocked = async (chainId, poolAddress, accountAddress) => {
  const { version } = await getLpConfig(chainId, poolAddress);
  if (version === 'slp') {
    return await isSlpUnlocked(chainId, poolAddress, accountAddress);
  // } else if (version === 'clp') {
  //   return await isClpUnlocked(chainId, poolAddress, accountAddress);
  } else if (version === 'clp') {
    return await isClp2Unlocked(chainId, poolAddress, accountAddress);
  }
};

export const getLpWalletBalance = async (
  chainId,
  poolAddress,
  accountAddress
) => {
  const { version } = await getLpConfig(chainId, poolAddress);
  if (version === 'slp') {
    return await getSlpWalletBalance(chainId, poolAddress, accountAddress);
  // } else if (version === 'clp') {
  //   return await getClpWalletBalance(chainId, poolAddress, accountAddress);
  } else if (version  === 'clp') {
    return await getClp2WalletBalance(chainId, poolAddress, accountAddress);
  }
};
