
import { getUserWalletBalance, DeriEnv, connectWallet, isUnlocked, openConfigListCache, unlock } from "../lib/web3js/index";
import config from '../config.json'
import { formatBalance, eqInNumber, storeChain, isLP } from "../utils/utils";
import { observable, computed, action, makeObservable, autorun } from "mobx";
import version from './Version'
import apiProxy from './ApiProxy'
import ApiProxy from "./ApiProxy";



class Wallet {

  detail = {}
  defaultNw = {}
  bTokenId = ''
  address = ''

  constructor() {
    makeObservable(this, {
      detail: observable,
      defaultNw: observable,
      bTokenId: observable,
      address: observable,
      setDefaultNw: action,
      setDetail: action,
      setBTokenId: action,
      setAddress: action,
      supportV2: computed,
      supportV1: computed,
      supportAllVersion: computed,
      supportChain: computed,
      supportOpen: computed,
      balance: computed,
      isApproved: computed
    })

    autorun(async () => {
      if (this.detail.chainId && this.detail.account && this.address) {
        let isApproved
        if (version.isOpen) {
          await openConfigListCache.update()
        }
        if (isLP(this.address)) {
          isApproved = await ApiProxy.request('isLpUnlocked', [this.detail.chainId, this.address, this.detail.account])
        } else {
          isApproved = await ApiProxy.request('isUnlocked', [this.detail.chainId, this.address, this.detail.account, this.bTokenId])
        }
        this.detail.isApproved = isApproved;
        this.setDetail(this.detail)
      }
    })

  }



  supportWeb3 = () => !!window.ethereum

  isConnected = () => !!this.detail.account;

  get isApproved() {
    return this.detail.isApproved
  }


  approve = async () => {
    if (this.detail.chainId && this.address) {
      let approved;
      if (version.isOpen) {
        await openConfigListCache.update()
      }
      if (isLP(this.address)) {
        approved = await ApiProxy.request('unlockLp', [this.detail.chainId, this.address, this.detail.account]);
      } else {
        approved = await ApiProxy.request('unlock', [this.detail.chainId, this.address, this.detail.account, this.bTokenId]);
      }

      this.detail.isApproved = approved
      this.setDetail(this.detail)
    }
  }

  connect = async () => {
    const data = await apiProxy.request('connectWallet')
    return new Promise(async (resolve, reject) => {
      if (data) {
        const { chainId, account } = data
        const wallet = await this.loadWalletBalance(chainId, account);
        resolve(wallet)
      } else {
        reject(null)
      }
    })
  }

  switchNetwork = async (network) => {
    if (!this.isConnected()) {
      this.connect();
    }
    const chainInfo = config[DeriEnv.get()]['chainInfo']
    const chainId = `0x${(parseInt(network.id)).toString(16)}`
    network = chainInfo[parseInt(network.id)]
    try {
      await window.ethereum.request({
        method: 'wallet_switchEthereumChain',
        params: [{ chainId }],
      });
    } catch (error) {
      // alert('err' + JSON.stringify(error))
      if (error.code === 4902) {
        try {
          await window.ethereum.request({
            method: 'wallet_addEthereumChain',
            params: [{ chainId, ...network.metamask }],
          });
        } catch (addError) {
          console.error('err', addError)
        }
      }
    }
  }

  loadWalletBalance = async (chainId, account) => {
    if (version.isOpen) {
      await openConfigListCache.update()
    }
    const data = await apiProxy.request('getUserWalletBalance', [chainId, account]).catch(e => console.log('wallet account is not exist'))
    const detail = { chainId, account, balance: data.balance, unit: data.unit, formatBalance: formatBalance(data.balance) }
    const env = DeriEnv.get();
    const { chainInfo } = config[env]

    if (chainInfo[chainId]) {
      Object.assign(detail, { ...chainInfo[chainId], supported: true })
      storeChain(chainInfo[chainId])
    }
    this.setDetail(detail)
    return detail;
  }

  get = () => {
    return this.detail;
  }

  setDetail(detail) {
    this.detail = detail;
  }

  setAddress(address) {
    this.address = address
  }

  setBTokenId(bTokenId) {
    this.bTokenId = bTokenId
  }

  setDefaultNw(network) {
    this.defaultNw = network
  }

  refresh() {
    this.loadWalletBalance(this.detail.chainId, this.detail.account);
  }

  isSupportChain(isOptions) {
    const chainId = this.detail.chainId
    const env = DeriEnv.get();
    const { chainInfo } = config[env]
    if (isOptions) {
      return chainInfo[chainId] && chainInfo[chainId]['supportOptions']
    } else {
      return this.supportChain
    }
  }

  get supportV2() {
    return this.detail.supportV2
  }

  get supportV1() {
    return this.detail.supportV1;
  }

  get supportAllVersion() {
    return this.detail.supportV1 && this.detail.supportV2
  }

  get supportChain() {
    return this.detail.supported
  }

  get supportInnovation() {
    return this.detail.supportInnovation || !this.isConnected()
  }

  get supportOpen() {
    return this.detail.supportOpen || !this.isConnected()
  }

  get balance() {
    return this.detail.balance
  }

  get balanceUnit() {
    return this.detail.unit && this.detail.unit.toUpperCase()
  }


}

export default Wallet;