import { getPoolVersionId } from '../shared/config/pool';
import { isDeriUnlocked } from '../shared/api';
import { checkAddress, checkChainId, LITE_VERSIONS } from '../shared/config';

import { getEstimatedLiquidatePriceV2 } from '../v2/api';

import { api } from './api_shared'
import { catchApiError, fetchJson, getHttpBase, bg } from '../shared/utils';

export const getSpecification = async (chainId, poolAddress, symbolId) => {
  const versionId = getPoolVersionId(poolAddress);
  if (versionId.failed) {
    return { success: false, response: versionId.response };
  }
  return api(versionId).getSpecification(chainId, poolAddress, symbolId);
};

