export const getUserFirstClaimInfo = async (accountAddress) => {
  return catchApiError(async () => {
    accountAddress = checkAddress(accountAddress);
    const res = await fetchJson(
      `${getHttpBase()}/first_claim_info/${accountAddress}`
    );
    if (res && res.success && res.data) {
      return {
        account: res.data.account,
        first_timestamp: res.data.block_timestamp,
        amount: res.data.amount || '',
      };
    }
  });
};

export const getPoolMarkPrices24hInfo = async (chainId, pools=[]) => {
  return catchApiError(async () => {
    chainId = checkChainId(chainId)
    let versionIds = []
    let newPools = []
    for (let i = 0; i < pools.length; i++) {
      pools[i] = checkAddress(pools[i]);
      const res = getPoolVersionId(pools[i])
      if (!res.failed) {
        versionIds.push(res)
        newPools.push(pools[i])
      } else {
        // invalid version
        // return res
      }
    }
    const markPrices = (await Promise.all(
      versionIds.map((versionId, index) =>
        api(versionId).getPoolMarkPrices(chainId, pools[index])
      )
    )).flat();
    const res = await Promise.all(
      newPools.reduce((acc, pool) => [
        ...acc,
        fetchJson(`${getHttpBase()}/symbol_markprice_24h/${pool}`),
      ], [])
    );
    const markPrices24h = res.map((r) => r.data).flat()
    // return markPrices.concat(markPrices24h);
    return markPrices.map((m, index) => {
      const markPrice24h = markPrices24h.find(
        (m24h) => m24h.pool === m.pool && m24h.symbolId === m.symbolId
      );
      // for old symbol
      if (markPrice24h) {
        const open = markPrice24h.open
        return {
          pool: m.pool,
          symbolId: m.symbolId,
          symbol: m.symbol,
          markPrice: m.markPrice,
          markPrice24h: open,
          volumn24h: bg(m.markPrice).minus(open).div(open).toString(),
        }
      } else {
        // for new added symbol
        return {
          pool: m.pool,
          symbolId: m.symbolId,
          symbol: m.symbol,
          markPrice: m.markPrice,
          markPrice24h: '0',
          volumn24h: '0',
        }
      }
    })
  });
};