import { openConfigListCache } from '../v2_lite_open/api/index';
import { checkEnv } from '../shared/config';
import { getJsonConfig } from '../shared/config/config';
import { catchApiError } from '../shared/utils';

export const getPoolConfigList = async (env, type=null) => {
  return catchApiError(async () => {
    env = checkEnv(env);
    let res = getJsonConfig(env);
    // add v1 config
    // res = res.concat(getPoolV1ConfigList(env));
    // const result = openConfigListCache.get();
    // if (result && Array.isArray(result) && result.length > 0) {
    //   res = [...res, ...result];
    // }
    if (!!type) {
      res = res.filter((c) => c.version === type)
    }
    return res;
  }, [env, type], []);
};
