export const getRestServerConfig = (env = 'dev') => {
  if (env === 'prod') {
    // for production
    return (
      (process && process.env && process.env.REACT_APP_REST_SERVER_URL) ||
      'https://api.deri.finance'
    );
    //return 'https://alphaapi.deri.finance';
  } else {
    // for test
    return (
      (process && process.env && process.env.REACT_APP_REST_SERVER_URL) ||
      'https://testnetapi.deri.finance'
    );
  }
};
